import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import actionHandlers from './actionHandlers';
import styles from './styles.scss';

/**
 * Internal email client quick message wrapper component
 *
 * ```jsx
 * <sn-email-client-internal-quick-message-wrapper
 * 		target-table={..}
 * 		target-record={..}
 * />
 * ```
 *
 * @seismicElement sn-email-client-internal-quick-message-wrapper
 * @summary Internal email client quick message wrapper component
 *
 */

createCustomElement('sn-email-client-internal-quick-message-wrapper', {
	renderer: {
		type: snabbdom,
		view
	},
	actionHandlers,
	styles,
	initialState: {
		quickMessages: null
	},
	properties: {
		/**
		 * Table name of the record againnst which Email Client
		 * needs to be launched
		 * @type {tableName}
		 */
		targetTable: { default: '' },
		/**
		 * Record sysId against which Email Client needs to be launched
		 * @type {sysId}
		 */
		targetRecord: { default: '' },
		/**
		 * Dirty fields in the form before the email client gets launched.
		 */
		dirtyFields: { default: [] }
	}
});
