import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './styles.scss';
import behaviors from './behaviors';

/**
 * Internal dropdown list component for email client
 *
 * ```jsx
 * <sn-email-client-internal-recipient-dropdown-list
 * 		data-state={..}
 * 		acitve-item={..}
 * 		items={..}
 * />
 * ```
 *
 * @seismicElement sn-email-client-internal-recipient-dropdown-list
 * @summary Internal dropdown list component for email client
 *
 */

createCustomElement('sn-email-client-internal-recipient-dropdown-list', {
	renderer: {
		type: snabbdom
	},
	view,
	properties: {
		/**
		 * An array of `DropdownListItem` or `DropdownSection` items.
		 * @type {Array.<DropdownListItem>}
		 */
		items: {
			default: []
		},
		/**
		 * ID of the current active descendant in the dropdown list.
		 * @type {string}
		 */
		activeItem: { default: null },

		dataState: { default: '' }
	},
	behaviors,
	styles
});
