import { isString } from 'lodash';
import { EMAIL_TABLES } from './constants';
import '../now-email-client-viewer-connected';
import '../now-email-client-composer-connected';

export default state => {
	let { table, sysId, params, tabId } = state.properties;
	if (isString(params)) {
		params = JSON.parse(params);
	}
	const { parentTable, parentSysId, dirtyFields, replyId, replyType } = params;
	if (table === EMAIL_TABLES.DRAFT_EMAIL) {
		return (
			<now-email-client-composer-connected
				sys-id={sysId}
				target-table={parentTable}
				target-record={parentSysId}
				dirty-fields={dirtyFields}
				draft-record-id={sysId}
				reply-type={replyType}
				reply-id={replyId}
				tab-id={tabId}
			/>
		);
	} else if (table === EMAIL_TABLES.SENT_EMAIL) {
		return <now-email-client-viewer-connected email-record-id={sysId} />;
	}
};
