import { getProperty } from 'sn-uxpage-presource';
export const defaultLanguage = getProperty('glide.sys.language', 'en');

export default class User {
	constructor(user) {
		if (typeof user === 'object') {
			this.sys_id = user.sys_id;
			this.userName = user.userName;
			this.language = user.language;
		}
	}

	set language(lang) {
		this._language = lang || defaultLanguage;
	}
	get language() {
		return this._language;
	}
}
