import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './styles.scss';
import actionHandlers from './actionHandlers';
import { HEADER_BUTTON_ACTIONS } from './constants';

/**
 * Internal email client header component
 *
 * ```jsx
 * <sn-email-client-internal-header
 * 		heading-value={..}
 * 		header-buttons={..}
 * />
 * ```
 *
 * @seismicElement sn-email-client-internal-header
 * @summary Internal email client header component
 *
 */

createCustomElement('sn-email-client-internal-header', {
	renderer: {
		type: snabbdom
	},
	view,
	properties: {
		/**
		 * The heading value - Expected to recieve a translated value.
		 * @type {string}
		 */
		headingValue: { default: '' },
		/**
		 * The array of header action buttons
		 * @type {Array.<HeaderActionButton>}}
		 */
		headerButtons: { default: [] },
		/**
		 * The size of top and bottom padding around header
		 * @type {string}
		 */
		padding: {default: 'large'}

	},
	styles,
	actionHandlers,
	dispatches: {
		/**
		 * Dispatched when a header action button is clicked.
		 * @type {{actionType: string}}
		 */
		[HEADER_BUTTON_ACTIONS.HEADER_ACTION_BUTTON_CLICKED]: {}
	}
});
