import { RECIPIENT_CONSTANTS } from '../../constants';
import { COMPOSITE_ACTIONS } from '../../../../constants/emailConstants';
import '@servicenow/now-pill';
import '@servicenow/now-tooltip';
import { t } from 'sn-translate';
// eslint-disable-next-line
import { createRef } from '@servicenow/ui-renderer-snabbdom';

const pillRef = {};
const genrateRef = id => {
	if (!pillRef[id]) {
		pillRef[id] = createRef();
	}

	return pillRef[id];
};

export const convertRecipientsToPills = (items, dataState, label) =>
	items.map(({ id, displayName, isValid, invalidReason }, index) => {
		const pillId = `${dataState}_${id}_${index}`;
		const elmRef = genrateRef(pillId);
		return (
			<span className="ec-recipient-picker-pill">
				<now-pill
					id={pillId}
					key={pillId}
					ref={elmRef}
					label={displayName}
					aria-label={t('{0} Recipient', label)}
					icon={isValid === false ? 'circle-exclamation-outline' : ''}
					canDismiss
					size="sm"
					className="recipient-pill"
					aria-describedby={pillId}
					append-to-payload={{
						dispatchedEvent: RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_REMOVED,
						itemIndex: index,
						dataState: dataState
					}}
				/>
				{!isValid ? (
					<now-tooltip
						id={id}
						key={index}
						target-ref={pillRef[pillId].current}
						content={t(`{0}`, invalidReason)}
						position={["bottom-center top-center"]}
					/>
				) : (
						''
					)}
			</span>
		);
	});

export const mapEndLabelsToDivs = () => (
	<div className="ec-recipient-picker-cc-bcc-button">
		<now-button
			id="ec-recipient-cc-bcc-button"
			variant="tertiary"
			size="md"
			label={t('Cc/Bcc')}
			append-to-payload={{
				dispatchedEvent: RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_UPDATE_STATE,
				showCcBcc: true,
				autofocus: true
			}}
		/>
	</div>
);

export const getNextItem = (items, activeItem, delta) => {
	const id = activeItem.emailAddress || activeItem.displayName;
	const lastActiveIndex = items.findIndex(({ emailAddress, displayName }) =>
		emailAddress ? emailAddress === id : displayName === id
	);

	const nextIndex = Math.min(
		Math.max(lastActiveIndex + delta, 0),
		items.length - 1
	);
	return items[nextIndex];
};

export const addActiveDescendant = (input, optionPrefix, activeItem) => {
	const id =
		activeItem.emailAddress || activeItem.displayName.replace(/\s+/g, '_');
	const activeDescendant = `${optionPrefix}-${id}`;
	input.setAttribute('aria-activedescendant', activeDescendant);
};

export const getItemByIndex = (items, index) => {
	return index < 0 ? items[items.length + index] : items[index];
};

export const removeRecipient = (index, dataState, dispatch) => {
	dispatch(RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_REMOVED, {
		itemIndex: index,
		dataState: dataState
	});
};

export const addPill = (item, dispatch, dataState) => {
	dispatch(COMPOSITE_ACTIONS.EMAIL_DRAFT_FIELD_MODIFIED);
	dispatch(RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_VALIDATION_STARTED, {
		...item,
		dataState
	});
};

export const collapseInput = (e, dispatch) => {
	dispatch(RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_CC_BCC_COLLAPSE, {
		relatedTarget: e.relatedTarget
	});
};

export const startSearch = (dispatch, dataState, chars) => {
	dispatch(RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_FETCH_STARTED, {
		qs: chars,
		dataState
	});
};

export const resetInputValue = e => {
	e.target ? (e.target.value = '') : null;
};

export const resetInputBoxScroll = e => {
	e.target.closest('.ec-recipient-picker-multiple').scroll(0, 0);
};

export const isEntireTextSelected = target => {
	const selectionRange = target.selectionEnd - target.selectionStart;
	return selectionRange === target.value.length ? true : false;
};
