const GLIDE_ATTACHMENT_QUERY = 'GlideAttachmentQuery_Query';
const GLIDE_EMAIL_MUTATION = 'GlideEmail_Mutation';
const GET_ATTACHMENTS = 'attachments';
const GLIDE_EMAIL_DELETE_ATTACHMENT = 'deleteAttachment';
const GLIDE_EMAIL_RENAME_ATTACHMENT = 'renameAttachment';
const GLIDE_EMAIL_UPLOAD_ATTACHMENT_FROM_TARGET_RECORD =
	'addAttachmentsFromTargetRecord';

const RECORD_ATTACHMENT_QUERY = `
query($table: String!, $sysId: String!) {
    ${GLIDE_ATTACHMENT_QUERY} {
        ${GET_ATTACHMENTS}(table: $table, sysId: $sysId) {
            records{
				name
				userId
				userName
				date
				dateLabel
				type
				state
				size
				sysId
				canEditName
				canDelete
			}
			types
			extensions
			canCreate
			fileNameLength
        }
    }
}`;

const EMAIL_CLIENT_DELETE_ATTACHMENT = `
mutation($sysId: String!) {
    ${GLIDE_EMAIL_MUTATION} {
        ${GLIDE_EMAIL_DELETE_ATTACHMENT}(id: $sysId)
    }
}`;

const EMAIL_CLIENT_RENAME_ATTACHMENT = `
mutation($sysId: String!, $name: String!) {
    ${GLIDE_EMAIL_MUTATION} {
        ${GLIDE_EMAIL_RENAME_ATTACHMENT}(id: $sysId, name: $name) {
            sysId
            draftId
            attachmentId
            contentDisposition
            attachment {
              sysId
              name
              size
              type
              state
            }
        }
    }
}`;

const UPLOAD_ATTACHMENT_FROM_TARGET_RECORD = `
mutation($draftId: String!, $attachmentIds: [String!]!) {
    ${GLIDE_EMAIL_MUTATION} {
        ${GLIDE_EMAIL_UPLOAD_ATTACHMENT_FROM_TARGET_RECORD}(draftId:$draftId, attachmentIds:$attachmentIds) {
            sysId
            draftId
            attachmentId
            contentDisposition
            attachment {
              sysId
              name
              size
              type
              state
            }
        }
    }
}`;

export const ATTACHMENTS_QUERY = {
	RECORD_ATTACHMENT_QUERY
};

export const ATTACHMENT_MUTATIONS = {
	EMAIL_CLIENT_DELETE_ATTACHMENT,
	EMAIL_CLIENT_RENAME_ATTACHMENT,
	UPLOAD_ATTACHMENT_FROM_TARGET_RECORD
};
