import { IMPORTANCE_MAP } from './constants';
export default class EmailViewerDataModel {
	constructor(object) {
		if (!object) {
			this.attachments = [];
			this.to = [];
			this.cc = [];
			this.bcc = [];
		} else {
			this.attachments = object.attachments;
			this.to = object.to;
			this.cc = object.cc;
			this.bcc = object.bcc;
			this.subject = object.subject;
			this.userInfo = object.userInfo;
			this.createdOn = object.createdOn;
			this.type = object.type;
			this.sysId = object.sysId;
			this.important = object.important;
			this.body = object.body;
			this.targetTable = object.targetTable;
			this.targetRecord = object.targetRecord;
		}
	}

	setRecipientTo(toObject) {
		this.to.push(toObject);
	}

	setRecipientCc(ccObject) {
		this.cc.push(ccObject);
	}

	setRecipientBcc(bccObject) {
		this.bcc.push(bccObject);
	}

	setValues(actionObject) {
		const {
			subject,
			body,
			body_text,
			sys_created_on,
			sys_id,
			importance,
			user_id,
			target_table,
			instance,
			type
		} = actionObject;
		if (user_id) {
			const { _reference } = user_id;
			let userInfo = {
				fullName: _reference.name ? _reference.name.value : null,
				avatarPath: _reference.avatar ? _reference.avatar.value : null
			};
			this.userInfo = userInfo;
		} else {
			this.userInfo = null;
		}
		this.targetTable = target_table ? target_table.value : null;
		this.targetRecord = instance ? instance.value : null;
		this.createdOn = sys_created_on ? sys_created_on.displayValue : null;
		this.type = type ? type.value : null;
		this.sysId = sys_id ? sys_id.value : null;
		this.important =
			importance && importance.value
				? IMPORTANCE_MAP[importance.value.toLowerCase()]
				: null;
		this.subject = subject ? subject.value : null;
		this.body = body
			? body.value
				? body.value
				: body_text
				? body_text.value
				: null
			: null;
	}

	setAttachmentValues(actionObject) {
		const { file_name, content_disposition, attachment } = actionObject;
		let attachmentObject = {
			fileName: file_name ? file_name.value : null,
			contentDisposition: content_disposition ? content_disposition.value : null
		};
		let attachmentContent;
		if (attachment) {
			const { _reference } = attachment;
			attachmentContent = {
				name: _reference.file_name ? _reference.file_name.value : null,
				state: _reference.state ? _reference.state.value : null,
				sysId: _reference.sys_id ? _reference.sys_id.value : null,
				size: _reference.size_bytes ? _reference.size_bytes.value : null,
				type: _reference.content_type ? _reference.content_type.value : null
			};
		}
		this.attachments.push({
			...attachmentObject,
			attachment: attachmentContent
		});
	}

	setAttachments(attachmentArray) {
		attachmentArray.forEach(item => this.setAttachmentValues(item));
	}

	getValues() {
		return {
			targetTable: this.targetTable,
			targetRecord: this.targetRecord,
			sysId: this.sysId,
			to: this.to,
			cc: this.cc,
			bcc: this.bcc,
			subject: this.subject,
			body: this.body,
			type: this.type,
			important: this.important,
			userInfo: this.userInfo,
			createdOn: this.createdOn,
			attachments: this.attachments
		};
	}
}
