import { RECIPIENT_CONSTANTS } from '../../constants';
import { keyUtils } from '../../../../commons/keyUtils';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { t } from 'sn-translate';
import '@servicenow/now-dropdown';
import '@servicenow/now-popover';
import './dropdownList/dropdownList';
import { pick } from 'lodash';
import {
	startSearch,
	addPill,
	resetInputValue,
	resetInputBoxScroll,
	removeRecipient,
	getNextItem,
	addActiveDescendant,
	getItemByIndex,
	collapseInput,
	convertRecipientsToPills,
	isEntireTextSelected
} from './utils';

let currentActiveInput = '';

export default (state, { dispatch }) => {
	const { componentId } = state;
	const {
		label,
		dataState,
		pillItems,
		availableItems,
		opened
	} = state.properties;

	const listId = `sn-email-client-internal-recipient-dropdown-list-${componentId}`;
	const optionPrefix = `${listId}-option`;
	const activeItem = state.activeItem || availableItems[0];
	const id = activeItem
		? activeItem.emailAddress || activeItem.displayName.replace(/\s+/g, '_')
		: undefined;
	const ariaActiveDescendant = opened ? `${optionPrefix}-${id}` : undefined;

	const handleInput = e => {
		currentActiveInput = dataState;
		const chars = e.target.value.trimStart();
		if (chars) {
			startSearch(dispatch, currentActiveInput, chars);
		}
	};

	const handleEnter = e => {
		const chars = e.target.value.trim();
		const item = opened ? activeItem : { emailAddress: chars };
		if (opened || chars) {
			addPill(item, dispatch, dataState);
		}
		resetInputValue(e);
	};

	const handleBackspace = e => {
		const charsLen = e.target.value.length;
		const { length } = pillItems;
		if (!charsLen && length) {
			removeRecipient(length - 1, dataState, dispatch);
		}
		if (isEntireTextSelected(e.target)) {
			dispatch(RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_UPDATE_STATE, {
				opened: false,
				recipientSearchResults: []
			});
		}
	};

	const setNextActive = e => {
		const delta = e.key === 'ArrowDown' ? 1 : -1;
		const nextItem = getNextItem(availableItems, activeItem, delta);
		if (nextItem) {
			addActiveDescendant(e.target, optionPrefix, nextItem); //hack for safari + VO
			dispatch(RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_DROPDOWN_ITEM_SET_ACTIVE, {
				activeItem: nextItem
			});
		}
	};

	const handleKeyDown = e => {
		if (keyUtils.isKeyEvent({ code: 8 }, e)) {
			handleBackspace(e);
		} else if ((opened && keyUtils.onDownArrow(e)) || keyUtils.onUpArrow(e)) {
			setNextActive(e);
			e.preventDefault();
		} else if (keyUtils.onEnter(e) || keyUtils.onTab(e)) {
			handleEnter(e);
			opened && e.preventDefault();
		} else if (keyUtils.isKeyEvent({ code: 36 }, e)) {
			getItemByIndex(availableItems, 0);
		} else if (keyUtils.isKeyEvent({ code: 35 }, e)) {
			getItemByIndex(availableItems, -1);
		}
	};

	const handleClickOnRecipientBox = e => {
		e.currentTarget.querySelector('input').focus();
	};

	const handleBlur = e => {
		handleEnter(e);
		resetInputBoxScroll(e);
		collapseInput(e, dispatch);
	};

	const handleKeyUp = e => {
		if (opened && keyUtils.onEscape(e)) {
			dispatch(RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_UPDATE_STATE, {
				opened: false,
				recipientSearchResults: []
			});
		}
	};

	return (
		<Fragment>
			<div className="ec-recipient-picker-label ec-label-input-container">
				<label id={`ec-field-label-${componentId}`} className="ec-field-label">
					{`${label}:`}
				</label>
				<now-popover
					hide-tail
					className="ec-recipient-picker-field"
					constrain={pick(
						RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_DROPDOWN_CONSTRAINTS,
						['width', 'minWidth', 'maxWidth']
					)}
					manage-opened
					opened={opened}
					interaction-type="listbox">
					<div
						slot="trigger"
						className="ec-recipient-picker-multiple"
						role="combobox"
						aria-expanded={String(opened)}
						aria-haspopup="listbox"
						aria-owns={`${listId}-listbox`}
						aria-controls={`${listId}-listbox`}
						on-click={handleClickOnRecipientBox}
						on-focus={handleClickOnRecipientBox}>
						{convertRecipientsToPills(pillItems, dataState, label)}
						<div className="ec-recipient-picker-autocomplete">
							<input
								id={`ec-field-input-${dataState}`}
								className="ec-field-input"
								aria-label={t(`${label} recipient`)}
								aria-autocomplete="list"
								aria-activedescendant={ariaActiveDescendant}
								role="searchbox"
								on-input={handleInput}
								on-keydown={handleKeyDown}
								on-keyup={handleKeyUp}
								on-blur={handleBlur}
							/>
						</div>
					</div>
					{opened && currentActiveInput === dataState ? (
						<sn-email-client-internal-recipient-dropdown-list
							slot="content"
							items={availableItems}
							activeItem={activeItem}
							dataState={dataState}
							screen-reader-id={listId}
						/>
					) : (
						undefined
					)}
				</now-popover>
			</div>
		</Fragment>
	);
};
