const KEY_ESCAPE = {
	code: 27,
	key: 'Escape'
};

const KEY_SPACE = {
	code: 32,
	key: ' '
};

const KEY_ENTER = {
	code: 13,
	key: 'Enter'
};

const KEY_LEFT_ARROW = {
	code: 37,
	key: 'ArrowLeft'
};

const KEY_RIGHT_ARROW = {
	code: 39,
	key: 'ArrowRight'
};

const KEY_UP_ARROW = {
	code: 38,
	key: 'ArrowUp'
};

const KEY_DOWN_ARROW = {
	code: 40,
	key: 'ArrowDown'
};

const KEY_TAB = {
	code: 9,
	key: 'Tab'
};

function isKeyEvent(expectedKey, event) {
	const { key, keyCode } = event;
	if (expectedKey.key && expectedKey.key === key) {
		return true;
	}
	if (expectedKey.code && expectedKey.code === keyCode) {
		return true;
	}
	return false;
}

function onKeyEvent(key, event, callback) {
	let found = isKeyEvent(key, event);
	if (found && callback) {
		callback();
	}
	return found;
}

export function onEnter(event, callback) {
	return onKeyEvent(KEY_ENTER, event, callback);
}

export function onTab(event, callback) {
	if (event.persist) {
		event.persist();
	}
	return !event.shiftKey && onKeyEvent(KEY_TAB, event, callback);
}

export function onShiftTab(event, callback) {
	return event.shiftKey && onKeyEvent(KEY_TAB, event, callback);
}

export function onEscape(event, callback) {
	return onKeyEvent(KEY_ESCAPE, event, callback);
}

export function onSpace(event, callback) {
	return onKeyEvent(KEY_SPACE, event, callback);
}

export function onLeftArrow(event, callback) {
	return onKeyEvent(KEY_LEFT_ARROW, event, callback);
}

export function onRightArrow(event, callback) {
	return onKeyEvent(KEY_RIGHT_ARROW, event, callback);
}

export function onUpArrow(event, callback) {
	return onKeyEvent(KEY_UP_ARROW, event, callback);
}

export function onDownArrow(event, callback) {
	return onKeyEvent(KEY_DOWN_ARROW, event, callback);
}

const keyUtils = {
	onEnter,
	onTab,
	onShiftTab,
	onEscape,
	onSpace,
	onLeftArrow,
	onRightArrow,
	onUpArrow,
	onDownArrow,
	isKeyEvent
};

export { keyUtils };
