import { t } from 'sn-translate';
import { COMPOSITE_ACTIONS } from '../../../constants/emailConstants';

export default (state, updateState, dispatch) => {
	return (
		<div className="ec-reply-to ec-label-input-container">
			<label htmlFor="ec-replyto-input" className="ec-field-label">
				{t('Reply To:')}
			</label>
			<input
				id="ec-replyto-input"
				className="ec-reply-to-input"
				type="text"
				on-change={e => {
					dispatch(COMPOSITE_ACTIONS.EMAIL_DRAFT_FIELD_MODIFIED);
					updateState({ replyToValue: e.target.value });
				}}
				value={state.replyToValue}
			/>
		</div>
	);
};
