const handleConfirmationModalButtonClick = ({
	state,
	action: { payload },
	dispatch,
	updateState
}) => {
	const {
		nowAction: { type }
	} = payload;
	if (type === NOW_ACTIONS.NOW_MODAL_OPENED_SET) {
		updateState({ showConfirmationModel: false });
		dispatch(COMPOSITE_ACTIONS.EMAIL_DRAFT_RECORD_SEND_CANCELED);
		return;
	}
	const {
		action: { id }
	} = payload;
	const { recipients_bcc, recipients_cc, recipients_to } = state;
	if (id === CONFIRMATION_MODAL_CONSTANTS.DONT_SEND) {
		updateState({ showConfirmationModel: false });
		dispatch(COMPOSITE_ACTIONS.EMAIL_DRAFT_RECORD_SEND_CANCELED);
	} else if (id === CONFIRMATION_MODAL_CONSTANTS.SEND) {
		const to = consolidateRecipients(recipients_to);
		const cc = consolidateRecipients(recipients_cc);
		const bcc = consolidateRecipients(recipients_bcc);
		dispatch(HEADER_CONSTANTS.EMAIL_CLIENT_SEND_DRAFT, {
			draftRecord: {
				sysId: state.emailRecord.sysId,
				author: state.author,
				subject: state.subjectValue,
				body: state.editorValue,
				replyTo: state.replyToValue,
				from: state.fromValue,
				to: to,
				cc: cc,
				bcc: bcc,
				important: state.important,
				targetTable: state.properties.targetTable,
				targetRecord: state.properties.targetRecord
			}
		});
		updateState({
			showConfirmationModel: false,
			status: EMAIL_RENDERING_STATUS.SENDING
		});
	}
};

const handleComponentPropChanged = ({ action: { payload }, dispatch, updateState}) => {
	const { value, previousValue, name } = payload;
	switch(name) {
		case 'sendDraft':
			if (value && !previousValue) 
				dispatch(HEADER_CONSTANTS.EMAIL_HEADER_SEND_BUTTON_CLICKED);
		break;
		case 'saveDraft':
			if (value && !previousValue)
				dispatch(HEADER_CONSTANTS.EMAIL_HEADER_SAVE_BUTTON_CLICKED);
		break;
		case 'quickMessages':
			if (value) {
				const { message, isInsert } = value;
				dispatch(EDITOR_CONSTANTS.QUICK_MESSAGE_INSERT_CONTENT, {
					message: message,
					isInsert: isInsert
				});
			}
		break;
		case 'responseObj':
			const {replyType, replyId} = value;
			if (replyType && replyId && !isEqual(value, previousValue)) {
				updateState({responseObjModified: true});
				dispatch(COMPOSITE_ACTIONS.EMAIL_DRAFT_REINITIALISE_TRIGGERED);
				dispatch(EMAIL_DRAFT_CONSTANTS.EMAIL_CREATE_RESPONSE_DRAFT, {
					emailRecordId: replyId,
					responseType: replyType
				});
			}
	}
};

