import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { isUndefined } from 'lodash';
import { t } from 'sn-translate';
import { getComponentsByTagNames } from '@devsnc/library-uxf';
import { EMAIL_COMPOSER } from './constants';

const getQuickMessageAttributes = (attributes) => {
	const {targetTable, targetRecord, dirtyFields} = attributes;
	return {
		targetTable,
		targetRecord,
		dirtyFields
	}
}

const getResponseTemplatesAttributes = (attributes) => {
	const {targetTable, targetRecord} = attributes;
	return {
		table: targetTable,
		sysId: targetRecord,
		channel: 'Email'
	}
}

/* eslint-disable no-console */
const QUICK_MESSAGE_ELEMENT = {
	componentTag: 'sn-email-client-internal-quick-message-wrapper',
	icon: 'document-plus-outline',
	label: t('Quick Messages'),
	order: 0,
	actionAttributes: getQuickMessageAttributes
};

const RESPONSE_TEMPLATE_ELEMENT = {
	componentTag: 'sn-response-templates',
	icon: 'checklist-outline',
	label: t('Response Templates'),
	order: 1,
	actionAttributes: getResponseTemplatesAttributes
}

const DYNAMIC_LOAD_COMPONENTS = [
	RESPONSE_TEMPLATE_ELEMENT
]

export const ELEMENT_TAGS = [
	QUICK_MESSAGE_ELEMENT,
	...DYNAMIC_LOAD_COMPONENTS
];

export const ELEMENT_TAG_STATE = {
	[QUICK_MESSAGE_ELEMENT.componentTag]: true,
	[RESPONSE_TEMPLATE_ELEMENT.componentTag]: false
};

export function getElementState(elementTag) {
	return ELEMENT_TAG_STATE[elementTag];
}

export function setElementState(elementTag, state) {
	const currentState = ELEMENT_TAG_STATE[elementTag];
	if (isUndefined(currentState)) {
		console.warn('Unregistered element tag: ', elementTag);
	}
	ELEMENT_TAG_STATE[elementTag] = state;
}

function getDisabledElement(elementTag, isConfigured) {
	const message = isConfigured
		? `${elementTag} is not enabled!`
		: `${elementTag} is not configured. Double check the tag name.`;

	// using a HOC pattern
	return createCustomElement('undefined-element', {
		renderer: {
			type: snabbdom
		},
		view: () => <div key={elementTag}>{message}</div>
	});
}

export function getElementTag(elementTag) {
	const isEnabled = ELEMENT_TAG_STATE[elementTag];
	if (isEnabled) {
		return elementTag;
	}
	return getDisabledElement(elementTag, typeof isEnabled !== 'undefined');
}

export const loadDynamicSidebarComponents = (dispatch) => {
	DYNAMIC_LOAD_COMPONENTS.forEach(component => {
		const { componentTag } = component;
		const isDefined = !!customElements.get(componentTag);
		if (isDefined) {
			setElementState(componentTag, true );
			return;
		}
		getComponentsByTagNames([componentTag]).catch((e) => {
			console.warn(e);
		});
		customElements.whenDefined(componentTag)
			.then(() => { 
				setElementState(componentTag, true); 
				dispatch(EMAIL_COMPOSER.SIDEBAR_COMPONENTS_UPDATED);
			})
	});
}

export function getAllEnabledStateElements() {
	return ELEMENT_TAGS.filter(
		element => ELEMENT_TAG_STATE[element.componentTag]
	);
}
