import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import actionHandlers from './actionHandlers';
import styles from './styles.scss';
import initialState from './initialState';
import { COMPOSITE_ACTIONS } from '../../constants/emailConstants';

/**
 * Component used to render the email client details component
 *
 * ```jsx
 * <now-email-client-composer-details
 * 		target-table={..}
 * 		target-record={..}
 * 		reply-type={..}
 * 		reply-id={..}
 * 		draft-record-id={..}
 * 		send-draft={..}
 * 		save-draft={..}
 * 		quick-messages={..}
 * 		show-email-details={..}
 * />
 * ```
 *
 * @seismicElement now-email-client-composer-details
 * @summary Component used to render the email client details component
 *
 */

createCustomElement('now-email-client-composer-details', {
	renderer: {
		type: snabbdom
	},
	view,
	properties: {
		/**
		 * Table name of the record againnst which Email Client
		 * needs to be launched
		 * @type {tableName}
		 */
		targetTable: { default: '' },
		/**
		 * Record sysId against which Email Client needs to be launched
		 * @type {sysId}
		 */
		targetRecord: { default: '' },
		/**
		 * Dirty fields in the form before the email client gets launched.
		 */
		dirtyFields: { default: [] },
		/**
		 * SysId of the record inside sys_email_draft table to be displayed. Only used if newRecord is false.
		 * @type {sysId}
		 */
		draftRecordId: { default: '' },
		/**
		 * The object containing the response operation to be performed.
		 * @type {ResponseOperationObject}
		 */
		responseObj: {default: {
			replyType: "",
			replyId: ""
		}},
		/**
		 * Flag to be set to initiate a send operation.
		 * @type {boolean}
		 */
		sendDraft: { default: false },
		/**
		 * Flag to be set to initiate a save operation.
		 * @type {boolean}
		 */
		saveDraft: { default: false },
		/**
		 * Flag to show/hide email details - True by default
		 * @type {boolean}
		 */
		showEmailDetails: { default: true },
		/**
		 * Object containing quick messages and configs
		 * @type {QuickMessageDetails}
		 */
		quickMessages: { default: {} },
		/**
		 * Flag indicating if details is from workspace tab composer
		 */
		tabComposerDetails: { default: false }
	},
	initialState,
	actionHandlers,
	styles,
	dispatches: {
		/**
		 * Dispatched when the email draft record has been initialised.
		 * @type {{subject: string, recordId: subject}}
		 */
		[COMPOSITE_ACTIONS.EMAIL_RECORD_INITIALISED]: {},
		/**
		 * Dispatched when an email field has been modified. Has a payload of subjec if and only if subject field is modified.
		 * @type {{subject: string}}
		 */
		[COMPOSITE_ACTIONS.EMAIL_DRAFT_FIELD_MODIFIED]: {},
		/**
		 * Dispatched when the draft record has been successfully send.
		 */
		[COMPOSITE_ACTIONS.EMAIL_DRAFT_RECORD_SEND_SUCESS]: {},
		/**
		 * Dispatched when the draft record send action has failed.
		 */
		[COMPOSITE_ACTIONS.EMAIL_DRAFT_RECORD_SEND_FAILED]: {},
		/**
		 * Dispatched when the draft record has been successfully saved.
		 */
		[COMPOSITE_ACTIONS.EMAIL_DRAFT_RECORD_SAVE_SUCESS]: {},
		/**
		 * Dispatched when the draft record save action has failed.
		 */
		[COMPOSITE_ACTIONS.EMAIL_DRAFT_RECORD_SAVE_FAILED]: {},
		/**
		 * Dispatched to communicate to the parent component to load doc viewer
		 */
		[COMPOSITE_ACTIONS.LOAD_DOCUMENT_VIEWER]: {}
	}
});
