import { createAttachmentCards } from '../../commons/EmailAttachments/AttachmentCards/AttachmentCards';
import { VALID_SENT_EMAIL_TYPES } from './constants';
import { ATTACHMENT_CONTENT_DISPOSITION } from '../../commons/EmailAttachments/constants';
import { HEADER_ACTION_BUTTONS } from './constants';

const cardsConfig = {
	selection: false,
	scroll: false,
	canEdit: false
};

export const createAttachmentTiles = (attachments, state, updateState) => {
	return createAttachmentCards(attachments, cardsConfig, state, updateState);
};

export const getAttachmentTileCount = attachments => {
	return attachments
		.filter(
			attachment =>
				attachment.contentDisposition &&
				attachment.contentDisposition.toLowerCase() ===
					ATTACHMENT_CONTENT_DISPOSITION.attachment
		)
		.map(item => {
			const { attachment, draftId, sysId } = item;
			return { ...attachment, draftId, attachmentId: sysId };
		});
};

export const checkEmailTypeAndSentActions = type => {
	if (VALID_SENT_EMAIL_TYPES.includes(type)) {
		return [
			HEADER_ACTION_BUTTONS.REPLY_BUTTON,
			HEADER_ACTION_BUTTONS.REPLY_ALL_BUTTON,
			HEADER_ACTION_BUTTONS.FORWARD_BUTTON
		];
	}
	return [];
};
