import { t } from 'sn-translate';
import { isDragDropEnabled } from '../constants';

export const createAttachmentDropOverlay = (
	state,
	updateState,
	dispatch,
	onDrop
) => {
	const onleaveState = {
		showAttachmentDropOverlay: false,
		browseTypePannelopened: false
	};
	return isDragDropEnabled
		? createDropOverlay(state, updateState, dispatch, onDrop, onleaveState)
		: null;
};
export const createEditorDropOverlay = (
	state,
	updateState,
	dispatch,
	onDrop
) => {
	const onleaveState = {
		showEditorDropOverlay: false,
		browseTypePannelopened: false
	};
	return isDragDropEnabled
		? createDropOverlay(state, updateState, dispatch, onDrop, onleaveState)
		: null;
};
export const createDropOverlay = (
	state,
	updateState,
	dispatch,
	onDrop,
	onDragLeaveState
) => {
	return (
		<div
			className="drag-drop-overlay"
			on-drop={onDrop} /*{e => onDrop(e, state, dispatch)}*/
			on-dragenter={e => {
				e.target.classList.add('disable-pointer-events');
				e.stopPropagation();
			}}
			on-dragleave={e => {
				updateState(onDragLeaveState);
				e.stopPropagation();
			}}>
			<div className="overlay-dashed-border">
				<div className="overlay-content">
					<now-icon
						icon="documents-outline"
						size="lg"
						className="overlay-content-icon"
					/>
					<div className="overlay-content-header">{t('Upload File')}</div>
					<div className="overlay-content-body">
						{t('Drop a file in to add it as an attachment')}
					</div>
				</div>
			</div>
		</div>
	);
};
