import { SUBJECT_CONSTANTS } from '../constants';
import { COMPOSITE_ACTIONS } from '../../../constants/emailConstants';

const handleImportantFlagToggle = ({ state, updateState, dispatch }) => {
	const { important } = state;
	dispatch(COMPOSITE_ACTIONS.EMAIL_DRAFT_FIELD_MODIFIED);
	updateState({ important: !important });
};

const handleEmailSubjectChange = ({
	action: { payload },
	updateState,
	dispatch
}) => {
	dispatch(COMPOSITE_ACTIONS.EMAIL_DRAFT_FIELD_MODIFIED, {
		subject: payload.value
	});
	updateState({ subjectValue: payload.value });
};

export default {
	[SUBJECT_CONSTANTS.EMAIL_SUBJECT_CHANGED]: {
		effect: handleEmailSubjectChange,
		stopPropagation: true
	},
	[SUBJECT_CONSTANTS.EMAIL_IMPORTANT_FLAG_CHANGED]: {
		effect: handleImportantFlagToggle,
		stopPropagation: true
	}
};
