import '@servicenow/now-heading';
import '@servicenow/now-button';
import { t } from 'sn-translate';

const displayHeaderActions = options => {
	return options.map(item => (
		<span className="ec-header-secondary-item">
			<now-button
				variant={item.variant}
				size="md"
				label={item.label}
				id={item.id}
				tooltip-content={`${item.label} ${t('Email')}`}
				append-to-payload={{
					headerType: item.type
				}}
			/>
		</span>
	));
};

export default state => {
	const { headingValue, headerButtons, padding } = state.properties;
	if (headingValue !== null) {
		return (
			<div className={`ec-header ec-header-padding-${padding}`}>
				<div className="ec-header-title">
					<now-heading
						label={headingValue}
						level="2"
						has-no-margin
						variant="header-primary"
						wont-wrap
					/>
				</div>
				<div className="ec-header-secondary-items">
					{displayHeaderActions(headerButtons)}
				</div>
			</div>
		);
	}
};
