import { ErrorMap } from './AttachmentErrorMap';
import { NOW_ACTIONS } from '../../../constants/nowConstants';

export const showAlert = (name, dispatch, args, ctx = window) => {
	const defaultAlert = ErrorMap['DEFAULT_ERROR'];
	let alert =
		typeof name === 'object'
			? name
			: ErrorMap.ATTACHMENTS_LIMIT.get(name) || ErrorMap[name];
	args = Array.isArray(args) ? args : [args];
	alert = typeof alert === 'function' ? alert.apply(ctx, args) : alert;
	const { id, status, icon, header, content, action } = {
		...defaultAlert,
		...alert
	};

	dispatch(NOW_ACTIONS.ADD_TO_ALERT_LIST, {
		alerts: [{ id, status, icon, header, content, action }]
	});
};
