import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import actionHandlers from './actionHandlers';
import view from './view';
import initialState from './initialState';
import styles from './styles.scss';

/**
 * Internal typeahead component for email client
 *
 * ```jsx
 * <sn-email-client-internal-typeahead
 * 		label={..}
 * 		data-state={..}
 * 		pill-items={..}
 * 		available-items={..}
 * 		auto-focus={..}
 * 		opened={..}
 * />
 * ```
 *
 * @seismicElement sn-email-client-internal-typeahead
 * @summary Internal typeahead component for email client
 *
 */

createCustomElement('sn-email-client-internal-typeahead', {
	renderer: {
		type: snabbdom
	},
	view,
	properties: {
		/**
		 * Label of typeahead field
		 */
		label: {},
		/**
		 * Datastate of typeahead field
		 */
		dataState: { default: [] },
		/**
		 * Item List which comes as pill
		 */
		pillItems: { default: [], cast: 'array' },
		/**
		 * Item List which comes as part of dropdown
		 */
		availableItems: { default: [] },

		autofocus: { schema: { type: 'boolean' } },

		opened: { default: false }
	},
	initialState,
	actionHandlers,
	styles
});
