import { getAllEnabledStateElements } from './sidebarDependencymanager';

export const getEmailClientSidebarActions = (
	dirtyFields,
	state
) => {
	const sidebarActions = getAllEnabledStateElements();
	const {targetTable, targetRecord} = state;
	return sidebarActions.map(actionElement => ({
		icon: actionElement.icon,
		label: actionElement.label,
		tooltip: actionElement.label,
		name: actionElement.label,
		actionComponent: actionElement.componentTag,
		actionAttributes: actionElement.actionAttributes({
			targetTable,
			targetRecord,
			dirtyFields
		})
	}));
};
