import '@servicenow/now-modal';
import { t } from 'sn-translate';

import { createAttachmentCards } from '../AttachmentCards/AttachmentCards';
import {
	ATTACHMENT_CONSTANTS,
	ATTACHMENT_STATES,
	ATTACHMENT_POSITION
} from '../constants';
import { getSelectedAttachmentCount } from '../utils';

export const browseAttachments = (state, updateState) => {
	let {
		targetRecord: { attachments = [] }
	} = state;

	attachments = attachments.filter(
		file => file.state.toLowerCase() !== ATTACHMENT_STATES.NOT_AVAILABLE
	);

	const cardsConfig = {
		selection: true,
		scroll: true,
		canEdit: false
	};

	const count = getSelectedAttachmentCount(attachments);
	const addLabel = t('Add') + (count ? t(` (${count})`) : '');
	const modalActions = [
		{
			id: 'Add',
			label: addLabel,
			variant: 'primary',
			disabled: count ? false : true
		},
		{ id: 'cancel', label: t('Cancel'), variant: 'secondary' }
	];

	return (
		<now-modal
			id="browse-from-record-modal"
			footer-actions={modalActions}
			header-label={t('Attachments From Record')}
			size="md"
			opened
			append-to-payload={{
				dispatchedEvent: ATTACHMENT_CONSTANTS.BROWSE_MODAL_ACTION_CLICKED
			}}>
			<div className="ec-browse-files-modal-content">
				<div className="ec-browse-files-modal-title">
					{attachments.length ? t('Select files to add as attachments:') : ''}
				</div>
				<div className="ec-browse-files-modal-cards">
					{attachments.length
						? createAttachmentCards(
								attachments,
								cardsConfig,
								state,
								updateState,
								ATTACHMENT_POSITION.MODAL
						  )
						: ''}
				</div>
				{attachments.length == 0 ? (
					<div className="ec-cards-empty">
						<div className="ec-cards-empty-icon" />
						<div className="ec-cards-empty-message">
							{' '}
							{t('No attachments to display.')}
						</div>
					</div>
				) : (
					''
				)}
			</div>
		</now-modal>
	);
};
