/* eslint-disable no-console */
import '@servicenow/now-record-html-editor';
import { ATTACHMENT_CONSTANTS } from '../../../commons/EmailAttachments/constants';
import './tinymceInit';
import { EDITOR_CONSTANTS, isDragDropEnabled } from '../constants';
import { editorPlugins, editorToolbar } from './utils';
import { onDrop } from './eventHandlers';
import { createEditorDropOverlay } from '../DragDrop/DragDrop';
import '@servicenow/now-icon';
import { t } from 'sn-translate';

export default (state, dispatch, updateState) => {
	const onOverlayDrop = e => onDrop(e, window.tinyMCE.activeEditor);
	return (
		<div className="ec-editor">
			{state.showEditorDropOverlay
				? createEditorDropOverlay(state, updateState, dispatch, onOverlayDrop)
				: ''}
			<now-record-html-editor
				name="sn-email-client-editor"
				plugins={editorPlugins}
				toolbar={editorToolbar}
				value={state.editorValue}
				language={state.currentUser.language}
				minLineCount={EDITOR_CONSTANTS.EMAIL_EDITOR_MIN_LINE_COUNT}
				placeholder={t('Compose your email here')}
				autofocus
				referringTable={state.emailRecord.table}
				referringRecordId={state.emailRecord.sysId}
				powerPasteHtmlImport="merge"
				on-file-drop={e => {
					dispatch(ATTACHMENT_CONSTANTS.ATTACH_FILES, {
						files: e.detail.files
					});
					updateState({ showEditorDropOverlay: false });
				}}
				on-image-drop={e => {
					dispatch(EDITOR_CONSTANTS.ATTACH_INLINE_IMAGES, {
						imgs: e.detail.imgs,
						files: e.detail.files
					});
					updateState({ showEditorDropOverlay: false });
				}}
				on-image-delete={e =>
					dispatch(EDITOR_CONSTANTS.DELETE_INLINE_IMAGES, {
						sysIds: e.detail.sysIds
					})
				}
				on-dragover={() => {
					if (!state.showEditorDropOverlay && isDragDropEnabled) {
						dispatch(EDITOR_CONSTANTS.FILE_DRAGOVER);
					}
				}}
			/>
		</div>
	);
};
