import { RECIPIENT_CONSTANTS } from '../../../constants';

const renderLabel = label => {
	return <span className="ec-dropdown-list-item-truncate">{label}</span>;
};

const renderLabels = item => {
	const sublabel = item.additionalFields.reduce((prev, { value }) => {
		if (value && prev) {
			return ` ${prev} | ${value} `;
		}
		return prev || value;
	}, item.emailAddress || '');

	return (
		<div className="ec-dropdown-list-item-labels">
			{renderLabel(item.displayName)}
			{sublabel ? (
				<span className="ec-dropdown-list-item-sublabel">
					<span className="ec-dropdown-list-item-truncate">{sublabel}</span>
				</span>
			) : (
				undefined
			)}
		</div>
	);
};

const isActive = ({ emailAddress, displayName }, activeItem) => {
	if (!activeItem) {
		return false;
	}
	return emailAddress
		? emailAddress === activeItem.emailAddress
		: displayName === activeItem.displayName;
};

export default (state, { dispatch }) => {
	const { componentId } = state;
	const { items, activeItem, dataState } = state.properties;

	const handleMouseHover = activeItem => () => {
		dispatch(RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_DROPDOWN_ITEM_SET_ACTIVE, {
			activeItem: activeItem
		});
	};

	const handleClick = e => {
		e.preventDefault();
		dispatch(RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_DROPDOWN_ITEM_CLICKED, {
			dataState: dataState,
			item: activeItem
		});
	};

	const handleMouseDown = e => {
		//avoid blur
		e.preventDefault();
	};

	return (
		<div className="ec-dropdown-list">
			<div
				id={`${componentId}-items`}
				className="ec-dropdown-list-items"
				role="listbox"
				screen-reader-behavior-id="listbox">
				{items.map(item => {
					const active = isActive(item, activeItem);
					const id = item.emailAddress || item.displayName.replace(/\s+/g, '_');
					return (
						<div
							id={id}
							key={`${id}-${componentId}`}
							screen-reader-behavior-id={`option-${id}`}
							role="option"
							aria-selected="false"
							className={`ec-dropdown-list-item  ${active && 'active'}`}
							on-mouseover={handleMouseHover(item)}
							on-mousedown={handleMouseDown}
							on-click={handleClick}>
							{renderLabels(item)}
						</div>
					);
				})}
			</div>
		</div>
	);
};
