/* eslint-disable no-console */
import { ATTACHMENT_PREVIEW_CONSTANTS, DOC_VIEWER_STATES, ATTACHMENT_PREVIEW_ERROR_MAP } from '../constants';
import { getComponentsByTagNames } from '@devsnc/library-uxf';
import { COMPOSITE_ACTIONS } from '../../../constants/emailConstants';
import { EMAIL_VIEWER_CONSTANTS } from '../../../components/now-email-client-viewer-connected/constants';
import { NOW_ACTIONS, ALERT_CONSTANTS } from '../../../constants/nowConstants';

const DOC_VIEWER_COMPONENT_TAG = "sn-document-viewer";

/*
 * Downloads a single component tag
 */
const loadComponent = async (componentTag) => {
  if (!componentTag) return;
  return getComponentsByTagNames([componentTag])
    .then(names => names)
};

const loadDocViewer = ( {updateState, state: {docViewerState}, action : {payload} }) => {
  if  (!payload.docViewerEnabled && docViewerState !== DOC_VIEWER_STATES.DISABLED) {
    updateState({ docViewerState: DOC_VIEWER_STATES.DISABLED });
    return;
  }
  else if (docViewerState !== DOC_VIEWER_STATES.ABSENT && 
      docViewerState === DOC_VIEWER_STATES.DISABLED) return;
      
  const isDefined = !!customElements.get(DOC_VIEWER_COMPONENT_TAG);
  if (isDefined) {
    updateState({docViewerState: DOC_VIEWER_STATES.READY});
    return;
  }
  loadComponent(DOC_VIEWER_COMPONENT_TAG)
    .catch(updateState({docViewerState: DOC_VIEWER_STATES.FAILED}));
  
  updateState({docViewerState: DOC_VIEWER_STATES.LOADING});

  customElements.whenDefined(DOC_VIEWER_COMPONENT_TAG)
  .then(() => 
    updateState({docViewerState: DOC_VIEWER_STATES.READY}))
  .catch(() =>
      updateState({docViewerState: DOC_VIEWER_STATES.FAILED}));
}

const handleDocViewerCloseOrDelete = ( {updateState} ) =>
    updateState({attachmentToPreview: null});


const loadAttachmentPreview = ( {updateState, action: {payload}, state, dispatch }) => {
  const { docViewerState } = state;
  if (ATTACHMENT_PREVIEW_ERROR_MAP.has(docViewerState)) {
    dispatch(NOW_ACTIONS.ADD_TO_ALERT_LIST, {
      alerts: [
        {
          id: `now_email_alert_${Date.now()}`,
          status: ALERT_CONSTANTS.STATUS.INFO,
          icon: ALERT_CONSTANTS.ICONS.INFO,
          content: ATTACHMENT_PREVIEW_ERROR_MAP.get(docViewerState),
          action: { type: ALERT_CONSTANTS.ACTION.TYPES.DISMISS }
        }
      ]
    });
    return;
  }
  updateState({attachmentToPreview: payload.item});
}

export default {
    [COMPOSITE_ACTIONS.LOAD_DOCUMENT_VIEWER]: {
        effect: loadDocViewer,
        stopPropagation: true
    },
    [EMAIL_VIEWER_CONSTANTS.LOAD_DOCUMENT_VIEWER]: {
      effect: loadDocViewer,
      stopPropagation: true
    },
    [ATTACHMENT_PREVIEW_CONSTANTS.ATTACHMENT_PREVIEW_LOAD]: {
      effect: loadAttachmentPreview,
      stopPropagation: true
    },
    [ATTACHMENT_PREVIEW_CONSTANTS.DOC_VIEWER_CLOSE_DELETE_ACTION_CLICKED]: {
      effect: handleDocViewerCloseOrDelete,
      stopPropagation: true
    }
}