import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './styles.scss';

/**
 * Internal quick message detail view  component for email client
 *
 * ```jsx
 * <sn-email-client-internal-quick-message-detail-view
 * 		selected-item={..}
 * 		items={..}
 * />
 * ```
 *
 * @seismicElement sn-email-client-internal-quick-message-detail-view
 * @summary Internal quick message detail view  component for email client
 *
 */

createCustomElement('sn-email-client-internal-quick-message-detail-view', {
	renderer: {
		type: snabbdom
	},
	view,
	properties: {
		items: { default: [] },
		selectedItem: { default: null }
	},
	styles
});
