const GLIDE_EMAIL_QUERY = 'GlideEmail_Query';
const GLIDE_DOMAIN_QUERY = 'GlideDomain_Query';
const GLIDE_EMAIL_MUTATION = 'GlideEmail_Mutation';

const GLIDE_EMAIL_GET_CONFIGURATION = 'getConfiguration';
const GLIDE_EMAIL_RETRIEVE_DRAFT = 'getDraftById';
const GLIDE_EMAIL_SEARCH_RECIPIENTS = 'searchRecipients';
const GLIDE_EMAIL_ADDRESS_VALIDATION = 'validateAddress';
const GLIDE_EMAIL_DOC_VIEWER_ENABLED_CHECK = 'getEmailTargetTableDocViewerFlag';

const GLIDE_EMAIL_CREATE_DRAFT_FROM_TARGET = 'createDraftFromTargetRecord';
const GLIDE_EMAIL_UPDATE_DRAFT_AND_SEND = 'updateDraftAndSend';
const GLIDE_EMAIL_UPDATE_DRAFT_RECORD = 'updateDraft';

const RETRIEVE_CURRENT_USER = `${GLIDE_DOMAIN_QUERY} {
    user {
        sys_id
        userName
        language
      }
}`;


const EMAIL_CLIENT_CONFIG_QUERY = `
query($targetTable: String!) {
    ${RETRIEVE_CURRENT_USER},
    ${GLIDE_EMAIL_QUERY} {
        ${GLIDE_EMAIL_GET_CONFIGURATION}(table: $targetTable) {
            sysId
	        name
        	displayFrom
	        displayReplyTo
	        recipientQualifiers
			emailAddressFilters
			toOptional
		},
		${GLIDE_EMAIL_DOC_VIEWER_ENABLED_CHECK}(table: $targetTable) {
			isEnabled
		}
    }
}`;

const EMAIL_CLIENT_RETRIEVE_DRAFT_RECORD = `
query($draftRecordId: String!) {
	${GLIDE_EMAIL_QUERY} {
		${GLIDE_EMAIL_RETRIEVE_DRAFT}(id: $draftRecordId) {
			sysId
			author
			subject
			targetTable
			targetRecord
			body
			important
			from
			fromChoices
			replyTo
			to {
				emailAddress
				displayName
				recipientSource
				recipientSourceTable
			}
			cc {
				emailAddress
				displayName
				recipientSource
				recipientSourceTable
			}
			bcc {
				emailAddress
				displayName
				recipientSource
				recipientSourceTable
            },
            attachments {
				attachmentId
				contentDisposition
				draftId
				sysId
				attachment {
					name
					size
					state
					sysId
					type
				}
			}
		}
	}
}`;

const EMAIL_CLIENT_RECIPIENT_QUERY = `
query($chars: String!, $draftId: String!) {
	${GLIDE_EMAIL_QUERY} {
		${GLIDE_EMAIL_SEARCH_RECIPIENTS}(chars: $chars, draftId: $draftId) {
			recipientSource
			recipientSourceTable
			emailAddress
			displayName
			additionalFields {
				name
				value
			}
		}
	}
}`;

const EMAIL_CLIENT_ADDRESS_VALIDATION = `
query($emailAddress: String!, $emailAddressFilterIds: [String!]) {
	${GLIDE_EMAIL_QUERY} {
		${GLIDE_EMAIL_ADDRESS_VALIDATION}(emailAddress: $emailAddress, emailAddressFilterIds: $emailAddressFilterIds) {
			isValid
			emailAddress
			invalidReason
		}
	}
}`;

const EMAIL_CLIENT_CREATE_DRAFT_FROM_RECORD = `
mutation($targetTable: String!, $sysId: String!, $input: [GlideEmail_EmailDraftDirtyFieldInputType]) {
	${GLIDE_EMAIL_MUTATION} {
		${GLIDE_EMAIL_CREATE_DRAFT_FROM_TARGET}(table: $targetTable, id: $sysId, dirtyFields: $input) {
			sysId
			author
			subject
			body
			important
			from
			fromChoices
			targetRecord
			targetTable
			replyTo
			to {
				sysId
				emailAddress
				displayName
				recipientSource
				recipientSourceTable
			}
			cc {
				sysId
				emailAddress
				displayName
				recipientSource
				recipientSourceTable
			}
			bcc {
				sysId
				emailAddress
				displayName
				recipientSource
				recipientSourceTable
			}
			attachments {
				attachmentId
				contentDisposition
				draftId
				sysId
				attachment {
					name
					size
					state
					sysId
					type
				}
			}
		}
	}
}`;

const EMAIL_CLIENT_SEND_DRAFT_RECORD = `
mutation($draftRecord: GlideEmail_DraftInput_updateDraftAndSend!) {
	${GLIDE_EMAIL_MUTATION} {
		${GLIDE_EMAIL_UPDATE_DRAFT_AND_SEND}(draft: $draftRecord) {
			result
			sentDraft {
				sysId
				author
				subject
				body
				from
				important
				fromChoices
				replyTo
				to {
					emailAddress
					displayName
					recipientType
					recipientSource
					recipientSourceTable
				}
				cc {
					emailAddress
					displayName
					recipientType
					recipientSource
					recipientSourceTable
				}
				bcc {
					emailAddress
					displayName
					recipientType
					recipientSource
					recipientSourceTable
				}
				attachments {
					attachment {
						name
						size
						state
						sysId
						type
					}
					sysId
					contentDisposition
				}
			}
		}
	}
}`;

const EMAIL_CLIENT_UPDATE_DRAFT_RECORD = `
mutation($draftRecord: GlideEmail_DraftInput_update!) {
	${GLIDE_EMAIL_MUTATION} {
		${GLIDE_EMAIL_UPDATE_DRAFT_RECORD}(draft: $draftRecord) {
			sysId
			subject
			body
			from
			fromChoices
			important
			replyTo
			to {
				emailAddress
				displayName
				recipientType
				recipientSource
				recipientSourceTable
			}
			cc {
				emailAddress
				displayName
				recipientType
				recipientSource
				recipientSourceTable
			}
			bcc {
				emailAddress
				displayName
				recipientType
				recipientSource
				recipientSourceTable
			}
		}
	}
}`;

export const EMAIL_QUERIES = Object.freeze({
	EMAIL_CLIENT_CONFIG_QUERY,
	EMAIL_CLIENT_RETRIEVE_DRAFT_RECORD,
	EMAIL_CLIENT_RECIPIENT_QUERY,
	EMAIL_CLIENT_ADDRESS_VALIDATION
});

export const EMAIL_MUTATIONS = Object.freeze({
	EMAIL_CLIENT_CREATE_DRAFT_FROM_RECORD,
	EMAIL_CLIENT_UPDATE_DRAFT_RECORD,
	EMAIL_CLIENT_SEND_DRAFT_RECORD
});
