/* eslint-disable no-console */
import { actionTypes } from '@servicenow/ui-core';
import { QUICK_MESSAGE_WRAPPER_CONSTANTS } from './constants';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { EMAIL_CLIENT_QUICK_MESSAGE_QUERY } from './schemas/datasources';

const handleComponentBoot = ({ state, dispatch, action }) => {
	const {
		properties: { targetTable, targetRecord, dirtyFields }
	} = state;
	dispatch(QUICK_MESSAGE_WRAPPER_CONSTANTS.RETRIEVE_QUICK_MESSAGES, {
		table: targetTable,
		id: targetRecord,
		dirtyFields: dirtyFields
	});
	const { host } = action.payload;
	const sidebarWidth = host.clientWidth;
	host.style.maxWidth = sidebarWidth + 'px';
};

const retrieveQuickMessages = createGraphQLEffect(
	EMAIL_CLIENT_QUICK_MESSAGE_QUERY,
	{
		variableList: ['table', 'id', 'dirtyFields'],
		successActionType:
			QUICK_MESSAGE_WRAPPER_CONSTANTS.RETRIEVE_QUICK_MESSAGES_SUCCESS,
		errorActionType:
			QUICK_MESSAGE_WRAPPER_CONSTANTS.RETRIEVE_QUICK_MESSAGES_FAILED
	}
);

const handleQuickMessageFetchSuccess = ({
	action: { payload },
	updateState
}) => {
	updateState({
		quickMessages: payload.data.GlideEmail_Query.getQuickMessages
	});
};

const handleQuickMessageFetchFailed = ({ action: { payload } }) => {
	console.warn('Quick message fetch failed', payload.error);
};

export default {
	[actionTypes.COMPONENT_BOOTSTRAPPED]: {
		effect: handleComponentBoot,
		stopPropagation: true
	},
	[QUICK_MESSAGE_WRAPPER_CONSTANTS.RETRIEVE_QUICK_MESSAGES]: {
		...retrieveQuickMessages,
		stopPropagation: true
	},
	[QUICK_MESSAGE_WRAPPER_CONSTANTS.RETRIEVE_QUICK_MESSAGES_SUCCESS]: {
		effect: handleQuickMessageFetchSuccess,
		stopPropagation: true
	},
	[QUICK_MESSAGE_WRAPPER_CONSTANTS.RETRIEVE_QUICK_MESSAGES_FAILED]: {
		effect: handleQuickMessageFetchFailed,
		stopPropagation: true
	}
};
