import { RECIPIENT_CONSTANTS } from '../constants';
import { t } from 'sn-translate';
import { NOW_ACTIONS, ALERT_CONSTANTS } from '../../../constants/nowConstants';
import { isEmpty } from 'lodash';

const displayInvalidReason = recipient => {
	if (
		recipient.invalidReason ===
		RECIPIENT_CONSTANTS.RECIPIENT_INVALID_FLAGS.INVALID
	) {
		return t('The recipient {0} is invalid.', recipient.emailAddress);
	} else if (
		recipient.invalidReason ===
			RECIPIENT_CONSTANTS.RECIPIENT_INVALID_FLAGS.BLACKLISTED ||
		recipient.invalidReason ===
			RECIPIENT_CONSTANTS.RECIPIENT_INVALID_FLAGS.NOT_ALLOWED
	) {
		return t(
			'The recipient {0} is blocked. Contact your system administrator for permission.',
			recipient.emailAddress
		);
	}
};

export const validateRecipients = (recipients, dispatch) => {
	let isValid = true;
	recipients.forEach(recipient => {
		if (!recipient.isValid && typeof recipient.isValid !== 'undefined') {
			isValid = false;
			dispatch(NOW_ACTIONS.ADD_TO_ALERT_LIST, {
				alerts: [
					{
						id: `now_email_alert_${Date.now()}`,
						status: ALERT_CONSTANTS.STATUS.CRITICAL,
						icon: ALERT_CONSTANTS.ICONS.CRITICAL,
						content: displayInvalidReason(recipient),
						action: { type: ALERT_CONSTANTS.ACTION.TYPES.DISMISS }
					}
				]
			});
		}
	});
	return isValid;
};

export const consolidateRecipients = recipients => {
	return recipients
		.filter(recipient => recipient.isValid)
		.map(
			({
				emailAddress,
				displayName,
				recipientSource,
				recipientSourceTable
			}) => ({
				emailAddress,
				displayName,
				recipientSource,
				recipientSourceTable
			})
		);
};

const isAttachmentUploading = attachments => attachments.some(attachment => attachment.uploading); 

export const validateAttachments = (attachments, dispatch) => {
	const isUploading = isAttachmentUploading(attachments);
	if (isUploading) {
		dispatch(NOW_ACTIONS.ADD_TO_ALERT_LIST, {
			alerts: [
				{
					id: `now_email_alert_${Date.now()}`,
					status: ALERT_CONSTANTS.STATUS.INFO,
					icon: ALERT_CONSTANTS.ICONS.INFO,
					content: t(
						'An attachment is still uploading. Try again when the upload is done.'
					),
					action: { type: ALERT_CONSTANTS.ACTION.TYPES.DISMISS }
				}
			]
		});
	}
	return isUploading;
};

export const checkRecipientToMandatory = (recipients_to, toOptional) => {
	return toOptional ? false : isEmpty(recipients_to)
}