import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import initialState from './initialState';
import actionHandlers from './actionHandlers';
import view from './view';
import styles from './styles.scss';
import { EMAIL_VIEWER_CONSTANTS } from './constants';

/**
 * Lets users, such as workspace agents, view sent emails to verify information and formatting.
 *
 * ```jsx
 * <now-email-client-viewer-connected
 * 		email-record-id={..}
 * />
 * ```
 *
 * @seismicElement now-email-client-viewer-connected
 * @summary Enables users to view sent emails.
 *
 */
export const NowEmailClientViewer = 'now-email-client-viewer-connected';
createCustomElement(NowEmailClientViewer, {
	renderer: {
		type: snabbdom
	},
	view,
	initialState,
	properties: {
		emailRecordId: {}
	},
	actionHandlers,
	styles,
	dispatches: {
		/**
		 * Dispatched when a response action is clicked on the email viewer header.
		 * @type {PreviewRecordPayload}
		 */
		[EMAIL_VIEWER_CONSTANTS.PREVIEW_RECORD]: {}
	}
});
