import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import initialState from './initialState';
import actionHandlers from './actionHandlers';
import styles from './styles.scss';
import { EMAIL_COMPOSER } from './constants';

/**
 * Full-sized experience for authoring and sending emails. 
 *
 * ```jsx
 * <now-email-client-composer-connected
 * 		sys-id={..}
 * 		target-table={..}
 * 		target-record={..}
 * 		reply-type={..}
 * 		reply-id={..}
 * 		show-email-details={..}
 * />
 * ```
 *
 * @seismicElement now-email-client-composer-connected
 * @summary An experience for authoring an email and sending it to a selected customer. 
 *
 */
export const NowEmailClientComposer = 'now-email-client-composer-connected';
createCustomElement(NowEmailClientComposer, {
	renderer: {
		type: snabbdom,
		view
	},
	initialState,
	actionHandlers,
	styles,
	properties: {
		/**
		 * The Sys ID of the email_draft record.
		 * @type {sysId}
		 */
		sysId: {},
		/**
		 * Table name of the record to open the email client from. 
		 * @type {tableName}
		 */
		targetTable: { default: '' },
		/**
		 * Sys ID of the record to open the email client from. 
		 * @type {sysId}
		 */
		targetRecord: { default: '' },
		/**
		 * Dirty fields in the form before the email client opens.
		 */
		dirtyFields: { default: [] },
		/**
		 * The reply type. Only used for response emails. Options include `reply`, `replyall`, and `forward`.
		 * @type {string}
		 */
		replyType: { default: '' },
		/**
		 * The Sys ID of the email to reply to. Only used for response emails.
		 * @type {sysId}
		 */
		replyId: { default: '' },
		/**
		 * When true, displays email details. Default value: false.
		 * @type {boolean}
		 */
		showEmailDetails: { default: true },
		/**
		 * Tab ID of the workspace tab to open the component from.
		 * @type {string}
		 */
		tabId: { default: '' }
	},
	dispatches: {
		/**
		 * Dispatched when an email field is modified to indicate a dirty draft.
		 * @type {{table: string, sysId: string, status: string}}
		 */
		[EMAIL_COMPOSER.RECORD_STATUS_CHANGED]: {},
		/**
		 * Dispatched to update the workspace tab header.
		 * @type {{table: string, sysId: string, recordDisplayValue: string}}
		 */
		[EMAIL_COMPOSER.FORM_UPDATE_HEADER]: {},
		/**
		 * Dispatched to close the workspace tab once the user clicks Send.
		 * @type {{type: string, tabid: string}}
		 */
		[EMAIL_COMPOSER.UPDATE_ONE_TAB]: {},
		/**
		 * Dispatched when the email draft initializes and renders.
		 * @type {{table: string, sysId: string, subject: string}}
		 */
		[EMAIL_COMPOSER.EMAIL_DRAFT_INITIALISED]: {},
		/**
		 * Dispatched when any field of the email draft is modified.
		 * @type {EmailDraftPayload}
		 */
		[EMAIL_COMPOSER.EMAIL_DRAFT_MODIFIED]: {},
		/**
		 * Dispatched when the email draft is succesfully saved.
		 * @type {EmailDraftPayload}
		 */
		[EMAIL_COMPOSER.EMAIL_DRAFT_SAVE_SUCCESS]: {},
		/**
		 * Dispatched when the email draft is succesfully sent.
		 * @type {EmailDraftPayload}
		 */
		[EMAIL_COMPOSER.EMAIL_DRAFT_SEND_SUCCESS]: {}
	}
});
