/* eslint-disable no-unused-vars */
import '../sn-email-client-internal-header';
import { HEADER_ACTION_BUTTONS } from './constants';
import '../now-email-client-composer-details';
import { SidebarElement } from '@servicenow/now-record-common';
import { getEmailClientSidebarActions } from './utils';
import '../sn-email-client-internal-quick-message-wrapper';
import isEmpty from 'lodash/isEmpty';
import '@servicenow/now-alert';
import PreviewAttachment from '../../commons/EmailAttachments/AttachmentPreview/AttachmentPreview';
import { DOC_VIEWER_STATES } from '../../commons/EmailAttachments/constants';

const getValidHeaderButtons = () => {
	return [HEADER_ACTION_BUTTONS.SAVE_BUTTON, HEADER_ACTION_BUTTONS.SEND_BUTTON];
};

const checkComponentLoad = recordId => (recordId ? true : false);

export default ( state, {dispatch} ) => {
	const {
		properties: {
			targetTable,
			targetRecord,
			dirtyFields,
			sysId,
			replyType,
			replyId,
			showEmailDetails
		}
	} = state;
	const {
		subject,
		initiateSave,
		initiateSend,
		sidebarExpanded,
		recordId,
		quickMessages,
		alertList = [],
		attachmentToPreview,
		docViewerState,
		sideBarActions
	} = state;
	const hasAlertList = alertList && !isEmpty(alertList);
	const isDocViewerLoading = attachmentToPreview && (docViewerState === DOC_VIEWER_STATES.LOADING);
	const docViewerLoaderClassName = isDocViewerLoading ? 'doc-viewer-loading' : '';
	const sideBarClassName = sidebarExpanded ? 'expanded ' : 'collapsed ';
	return (
		<div className="ec-email-composer">
			{attachmentToPreview ? PreviewAttachment(attachmentToPreview, docViewerState, dispatch) : ''}
			{checkComponentLoad(recordId) ? (
					<div className={`ec-email-composer-header-container ${docViewerLoaderClassName}`}>
						<sn-email-client-internal-header
							heading-value={subject}
							header-buttons={getValidHeaderButtons()}
						/>
					</div>
				) : ''}
			{checkComponentLoad(recordId) ? (
				<hr className={`ec-email-composer-divider ${docViewerLoaderClassName}`} />
			) : (
				''
			)}
			<div className={`ec-email-composer-layout ${docViewerLoaderClassName}`}>
				<div className={"ec-email-composer-layout-" + 
						(sidebarExpanded ? 'details' : 'details-expanded')}>
					<div className="ec-email-composer-layout-details-component">
						<now-email-client-composer-details
							target-table={targetTable}
							target-record={targetRecord}
							dirty-fields={dirtyFields}
							draft-record-id={sysId}
							response-obj={{replyType, replyId}}
							send-draft={initiateSend}
							save-draft={initiateSave}
							show-email-details={showEmailDetails}
							quick-messages={quickMessages}
							tab-composer-details
						/>
					</div>
				</div>
				{checkComponentLoad(recordId) ? (
					<div
						className={`ec-email-composer-layout-sidebar-${sideBarClassName} ${docViewerLoaderClassName}`}>
						<SidebarElement
							actions={sideBarActions}
							classic-form={true}
							opened={sidebarExpanded}
							manage-opened
						/>
					</div>
				) : (
					''
				)}
			</div>
			{hasAlertList ? (
					<div className={`ec-alerts-container ${docViewerLoaderClassName}`}>
						<span aria-live="assertive">
							<now-alert-list
								items={alertList}
								manage-items={true}
								className="ec-alerts"
							/>
						</span>
					</div>
				) : ''
			}
		</div>
	);
};
