/* eslint-disable no-console */
import { createHttpEffect } from '@servicenow/ui-effect-http';
import resources from '../../../commons/EmailAttachments/schemas/restQueries';
import { showAlert } from '../../../commons/EmailAttachments/AttachmentErrors/AttachmentErrorHandler';
import { get } from 'lodash';
import { ErrorMap } from '../../../commons/EmailAttachments/AttachmentErrors/AttachmentErrorMap';

import { ATTACHMENT_QUERY_CONSTANTS } from '../../../commons/EmailAttachments/constants';
import { EDITOR_CONSTANTS } from '../constants';
import { COMPOSITE_ACTIONS } from '../../../constants/emailConstants';

let imgNodes = [];
const attachInlineImage = ({ action, state, dispatch }) => {
	//If no Sys_Id then no Email recrod yet to save the images
	if (!state.emailRecord.sysId) {
		return;
	}
	const { table, sysId } = state.emailRecord;
	const files = action.payload.files || [];
	imgNodes = action.payload.imgs || [];

	files.forEach(file => {
		const data = new FormData();
		data.append(ATTACHMENT_QUERY_CONSTANTS.SYSPARAM_TABLE, table);
		data.append(ATTACHMENT_QUERY_CONSTANTS.SYSPARAM_SYS_ID, sysId);
		data.append(ATTACHMENT_QUERY_CONSTANTS.FILE, file);
		dispatch(EDITOR_CONSTANTS.UPLOAD_INLINE_IMAGES, { data });
	});
};

const uploadInlineImage = createHttpEffect(resources.UPLOAD_INLINE_IMAGE.url, {
	...resources.UPLOAD_INLINE_IMAGE.options,
	successActionType: EDITOR_CONSTANTS.UPLOAD_INLINE_IMAGES_SUCCESS,
	errorActionType: EDITOR_CONSTANTS.UPLOAD_INLINE_IMAGES_FAIL,
	progressActionType: EDITOR_CONSTANTS.UPLOAD_PROGRESS
});

const onUploadInlineImageSuccess = ({
	action: {
		payload: { result }
	}
}) => {
	const { file_name, sys_id } = result;
	let img = imgNodes.find(node => node.dataset.fileName === file_name);
	if (img) {
		img.src = `/sys_attachment.do?sys_id=${sys_id}`;
		img.dataset.sysId = sys_id;
	}

	setTimeout(() => {
		const editor = window.tinyMCE.activeEditor;
		editor.focus();
		editor.execCommand('mceAutoResize');
	}, 400);
};

const onUploadInlineImageFail = ({ action, dispatch }) => {
	const formData = get(action, 'meta.request.data');
	const file = formData && formData.get('file');
	let fileName = (file && file.name) || '';
	const defaultAlert = ErrorMap['DEFAULT_ERROR'];
	const message = get(action, 'payload.data.message', '');
	const nwError = get(action, 'payload.message', '');
	const serverError = get(action, 'payload.data.error');
	let content;

	if (nwError === 'Network Error') {
		content = ErrorMap['ATTACHMENT_UPLOAD_NETWORK_ERROR'](fileName);
	} else if (serverError === 'Request Entity Too Large') {
		content = ErrorMap['ATTACHMENT_UPLOAD_SIZE_ERROR'](
			fileName,
			message.split(' ').pop()
		);
	} else {
		content = ErrorMap['ATTACHMENT_UPLOAD_ERROR'](fileName);
	}
	showAlert({ ...defaultAlert, ...content }, dispatch);
};

const deleteInlineImages = ({
	action: {
		payload: { sysIds = [] }
	},
	dispatch
}) => {
	sysIds.forEach(sysId => {
		if (sysId) {
			dispatch(EDITOR_CONSTANTS.DELETE_INLINE_IMAGE, { sysId });
		}
	});
};

const deleteInlineImage = createHttpEffect(resources.DELETE_INLINE_IMAGE.url, {
	...resources.DELETE_INLINE_IMAGE.options,
	successActionType: EDITOR_CONSTANTS.DELETE_INLINE_IMAGE_SUCCEESS,
	errorActionType: EDITOR_CONSTANTS.DELETE_INLINE_IMAGE_FAIL
});

const deleteInlineImageSuccess = coeffect => {
	const { action } = coeffect;
	const url = get(action, 'meta.request.updatedUrl');
	const sysId = url.split('/').pop();
	console.info(`Delete Success: Image '${sysId}' successfully deleted.`);
};

const deleteInlineImageFail = ({ action, dispatch }) => {
	const url = get(action, 'meta.request.updatedUrl');
	const sysId = url.split('/').pop();
	showAlert('ATTACHMENT_DELETE_FAILED', dispatch, sysId);
};

const uploadProgress = ({ action: payload }) => {
	const total = payload.payload.progressEvent.total;
	const loaded = payload.payload.progressEvent.loaded;
	const uploadPercent = (loaded / total) * 100;
	console.log('Upload Progress :', uploadPercent, '%');
};

const onDragOver = ({ updateState }) =>
	updateState({
		showEditorDropOverlay: true,
		showAttachmentDropOverlay: false
	});

const addHtmlToEditor = ({
	action: {
		payload: { message, isInsert }
	}
}) => {
	const { body } = message;
	if (isInsert) {
		window.tinyMCE.activeEditor.insertContent(body);
	} else {
		window.tinyMCE.activeEditor.setContent(body);
	}
};

const editorContentsChanged = ({ action, dispatch, updateState }) => {
	dispatch(COMPOSITE_ACTIONS.EMAIL_DRAFT_FIELD_MODIFIED);
	updateState({ editorValue: action.payload.value, shouldRender: false });
};

export default {
	[EDITOR_CONSTANTS.ATTACH_INLINE_IMAGES]: {
		effect: attachInlineImage,
		stopPropagation: true
	},
	[EDITOR_CONSTANTS.UPLOAD_INLINE_IMAGES]: {
		...uploadInlineImage,
		stopPropagation: true
	},
	[EDITOR_CONSTANTS.UPLOAD_INLINE_IMAGES_SUCCESS]: {
		effect: onUploadInlineImageSuccess,
		stopPropagation: true
	},
	[EDITOR_CONSTANTS.UPLOAD_INLINE_IMAGES_FAIL]: {
		effect: onUploadInlineImageFail,
		stopPropagation: true
	},
	[EDITOR_CONSTANTS.DELETE_INLINE_IMAGES]: {
		effect: deleteInlineImages,
		stopPropagation: true
	},
	[EDITOR_CONSTANTS.DELETE_INLINE_IMAGE]: {
		...deleteInlineImage,
		stopPropagation: true
	},
	[EDITOR_CONSTANTS.DELETE_INLINE_IMAGE_SUCCEESS]: {
		effect: deleteInlineImageSuccess,
		stopPropagation: true
	},
	[EDITOR_CONSTANTS.DELETE_INLINE_IMAGE_FAIL]: {
		effect: deleteInlineImageFail,
		stopPropagation: true
	},

	[EDITOR_CONSTANTS.UPLOAD_PROGRESS]: {
		effect: uploadProgress,
		stopPropagation: true
	},
	[EDITOR_CONSTANTS.FILE_DRAGOVER]: {
		effect: onDragOver,
		stopPropagation: true
	},
	[EDITOR_CONSTANTS.QUICK_MESSAGE_INSERT_CONTENT]: {
		effect: addHtmlToEditor,
		stopPropagation: true
	},
	[EDITOR_CONSTANTS.EDITOR_VALUE_CHANGED]: {
		effect: editorContentsChanged,
		stopPropagation: true
	}
};
