import '@servicenow/now-avatar';
import '@servicenow/now-button';
import '@servicenow/now-pill';
import '@servicenow/now-icon';
import { t } from 'sn-translate';
import { EMAIL_VIEWER_CONSTANTS } from '../constants';
import '@servicenow/now-highlighted-value';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';

const convertRecipientsIntoPills = (recipients, dataState) => {
	return recipients.map(item => (
		<div className="ec-viewer-recipients">
			<now-pill
				className={`ec-viewer-recipients-${dataState}`}
				key={item.sysId}
				label={item.name ? item.name : item.email}
				size="sm"
				selected={false}
				manage-selected
			/>
		</div>
	));
};

export default options => {
	const { userInfo, to, cc, bcc, showDetails, createdOn, important } = options;
	const { fullName, avatarPath } = userInfo;
	const imgSrc = `/${avatarPath}.iix`;

	return (
		<div className="ec-details">
			<div className="ec-details-user">
				<div className="ec-details-user-avatar">
					<now-avatar size="md" user-name={fullName} image-src={imgSrc} />
				</div>
				<div className="ec-details-user-more">
					<span
						className="ec-details-username"
						aria-label={t('sent by {0}', fullName)}>
						{fullName}
					</span>
					<div className="ec-details-user-more-date-important">
						<span
							className="ec-details-date"
							aria-label={t('sent on {0}', createdOn)}>
							{createdOn}
						</span>
						{important ? (
							<now-highlighted-value
								color="high"
								icon="flag-outline"
								label={t('Important')}
								show-icon
								variant="tertiary"
							/>
						) : (
							''
						)}
					</div>
				</div>
			</div>
			<div className="ec-details-recipients">
				<div
					className={
						'ec-details-recipients-components ' +
						(showDetails ? 'margin-bottom' : '')
					}>
					<div
						aria-label={t('sent to following')}
						className="ec-viewer-details-label"
						htmlFor="ec-viewer-to">
						{t('To:')}
					</div>
					<div className="ec-viewer-to-more">
						<span className="ec-viewer-details-list " id="ec-viewer-to">
							{convertRecipientsIntoPills(to, 'to')}
						</span>
					</div>
				</div>
				{showDetails ? (
					<Fragment>
						{cc.length ? (
							<div className="ec-details-recipients-components cc margin-bottom">
								<div
									aria-label={t('carbon copied to following')}
									className="ec-viewer-details-label"
									htmlFor="ec-viewer-cc">
									{t('Cc:')}
								</div>
								<span className="ec-viewer-details-list" id="ec-viewer-cc">
									{convertRecipientsIntoPills(cc, 'cc')}
								</span>
							</div>
						) : (
							''
						)}
						{bcc.length ? (
							<div className="ec-details-recipients-components bcc margin-bottom">
								<div
									aria-label={t('blind carbon copied to following')}
									className="ec-viewer-details-label"
									htmlFor="ec-viewer-bcc">
									{t('Bcc:')}
								</div>
								<span className="ec-viewer-details-list" id="ec-viewer-bcc">
									{convertRecipientsIntoPills(bcc, 'bcc')}
								</span>
							</div>
						) : (
							''
						)}
					</Fragment>
				) : (
					''
				)}
				{cc.length || bcc.length ? (
					<now-button-bare
						id="ec-viewer-showmore"
						size="sm"
						label={!showDetails ? t('Show Details') : t('Hide Details')}
						variant="secondary"
						append-to-payload={{
							dispatchedEvent:
								EMAIL_VIEWER_CONSTANTS.SHOW_DETAILS_BUTTON_PRESSED
						}}
					/>
				) : (
					''
				)}
			</div>
		</div>
	);
};
