import { createAddAttachmentAction } from '../AttachmentsAdd/AddAttachments';
import { createAttachmentCards } from '../AttachmentCards/AttachmentCards';
import { browseAttachments } from '../AttachmentsBrowse/AttachmentsBrowse';

export default (state, dispatch, updateState) => {
	const cardsConfig = {
		selection: false,
		scroll: false,
		canEdit: true
	};
	const { openBrowseModal, attachments } = state;

	return (
		<div
			className="ec-attachments"
			on-dragover={e => {
				e.preventDefault();
			}}>
			{createAddAttachmentAction(updateState, dispatch)}
			{createAttachmentCards(attachments, cardsConfig, state, updateState)}
			{openBrowseModal ? browseAttachments(state, updateState, dispatch) : ''}
		</div>
	);
};
