import { createElementFromNode } from '@servicenow/ui-renderer-snabbdom';
import { findTabbableNodes } from '@servicenow/library-enhanced-utils';

export const convertMarkup = (body, isTruncate = false) => {
	if (!document || !document.createElement) {
		return;
	}
	let fragment = document.createElement('Fragment');
	fragment.innerHTML = body || '';
	fragment.id = 'quick-message-body-content';

	if (isTruncate && body) {
		const nodes = fragment.querySelectorAll('p, li, div');
		let index = 0;
		while (
			nodes[index] &&
			nodes[index].innerText.trim() === '' &&
			index < nodes.length - 1
		) {
			index++;
		}
		fragment = nodes[index] || fragment;
		fragment.setAttribute('class', 'quick-message-panel-body-card-content');
	}
	return createElementFromNode(fragment);
};

export const focusFirstChild = node => {
	let focusableNodes = (0, findTabbableNodes)(node);
	if (focusableNodes.length) {
		focusableNodes[0].focus();
	}
};
