const handleAttachmentDownload = ({ action: { payload }, state }) => {
	const { sysId } = payload;
	const host = state.meta.host;
	const link = host.shadowRoot.querySelector('#download_attachment_link');
	if (sysId && link) {
		link.setAttribute('href', `/sys_attachment.do?sys_id=${sysId}`);
		link.click();
	}
};

const getEmailDocViewerFlag = (targetTable, dispatch) => {
	dispatch(DOC_VIEWER_CONSTANTS.FETCH_DOC_VIEWER_FLAG, {
		table: targetTable
	});
}

const fetchDocViewerFlag = createGraphQLEffect(
	EMAIL_VIEWER_QUERIES.DOC_VIEWER_FLAG_QUERY,
	{
		variableList: ['table'],
		successActionType: DOC_VIEWER_CONSTANTS.FETCH_DOC_VIEWER_FLAG_SUCCESS,
		errorActionType: DOC_VIEWER_CONSTANTS.FETCH_DOC_VIEWER_FLAG_FAILURE
	}
);

const docViewerFlagFetchSuccess = ({action: {payload}, updateState, dispatch, state: {emailDataObject}}) => {
	const dataObj = emailDataObject;
	const {attachments} = dataObj.getValues();
	const isDocViewerEnabled = payload.data.GlideEmail_Query.getEmailTargetTableDocViewerFlag.isEnabled;
	if (attachments.length) {
		dispatch(EMAIL_VIEWER_CONSTANTS.LOAD_DOCUMENT_VIEWER, {
			docViewerEnabled: isDocViewerEnabled
		})
	}
	updateState({docViewerEnabled: isDocViewerEnabled});
}

const docViewerFlagFetchFailure = ({ action }) => {
	console.error('Failed to fetch record', action);
};

const handleEmailRecordFetchSuccess = ({
	action: { payload },
	updateState,
	dispatch
}) => {
	if (payload.data) {
		let EmailDataObject = new EmailViewerDataModel();
		const EMAIL_QUERY_PATH =
			payload.data.GlideRecord_Query.sys_email._results[0];
		const ATTACHMENT_QUERY_PATH =
			payload.data.GlideRecord_Query.sys_email_attachment._results;
		EmailDataObject.setValues(EMAIL_QUERY_PATH);
		EmailDataObject.setAttachments(ATTACHMENT_QUERY_PATH);
		const { direct, copied, blind_copied, target_table } = EMAIL_QUERY_PATH;
		updateState({ emailDataObject: EmailDataObject });
		getRecipientInformation(direct, copied, blind_copied, dispatch);
		getEmailDocViewerFlag(target_table.value, dispatch);
	}
};

const handleEmailRecordFetchFailure = ({ action }) => {
	console.error('Failed to fetch record', action);
};

const createEmailResponseDraft = createGraphQLEffect(
	COMMON_EMAIL_MUTATIONS.EMAIL_CLIENT_CREATE_RESPONSE_DRAFT,
	{
		variableList: ['emailRecordId', 'responseType'],
		successActionType:
			EMAIL_VIEWER_CONSTANTS.EMAIL_CREATE_RESPONSE_DRAFT_SUCCESS,
		errorActionType: EMAIL_VIEWER_CONSTANTS.EMAIL_CLIENT_CREATE_RESPONSE_DRAFT
	}
);

/**
 * Here we are firing an API to create a ResponseDraft and monitoring it's result.
 * An error in the payload would result in displaying an alert message in the viewer,
 * whereas a valid sysId would then be dispatched with a PREVIEW_RECORD action.
 * Now ideally, all we would have needed to do is to dispatch a PREVIEW_RECORD actions with a sysId '-1' and a responseType,
 * which would have reduced the load time for a responseDraft.
 * But this wouldn't enable us to display the alert message inside the viewer.
 * This internal KungFu is unfortunate and would be replaced when a valid API for sys_email records is created! Peace to the world!
 */

const handleResponseButtonClicked = ({
	dispatch,
	state: { properties },
	action: { payload }
}) => {
	const { actionType } = payload;
	dispatch(EMAIL_VIEWER_CONSTANTS.EMAIL_CREATE_RESPONSE_DRAFT, {
		emailRecordId: properties.emailRecordId,
		responseType: actionType
	});
};

const handleResponseCreationFail = ({ action }) => {
	if (action.error) {
		console.error('GraphQL Error', action);
	}
};

const handleResponseCreationSuccess = ({ action: { payload }, dispatch, state }) => {
	if (payload.errors && payload.errors.length) {
		payload.errors.forEach(error => {
			const { message } = error;
			if (SEND_ERROR_MAP.has(message)) {
				dispatch(NOW_ACTIONS.ADD_TO_ALERT_LIST, {
					alerts: [
						{
							id: `now_email_alert_${Date.now()}`,
							status: ALERT_CONSTANTS.STATUS.CRITICAL,
							icon: ALERT_CONSTANTS.ICONS.CRITICAL,
							content: SEND_ERROR_MAP.get(message),
							action: { type: ALERT_CONSTANTS.ACTION.TYPES.DISMISS }
						}
					]
				});
			}
		});
	} else if (payload.data) {
		const QUERY_PATH = payload.data.GlideEmail_Mutation.createResponseDraft;
		const { meta: { host } } = state;
		dispatchClassicAndUibActions(host, 'PREVIEW_RECORD', dispatch, {
			table: 'sys_email_draft',
			sys_id: QUERY_PATH.sysId
		});
	}
};

const addToAlertList = ({ action, state, updateState }) =>
	updateState({
		alertList: [...state.alertList, ...(action.payload.alerts || [])]
	});

const dismissAlertEffect = ({ action, updateState }) =>
	updateState({
		alertList: action.payload.value || []
	});

