import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';

/**
 * Component assigned us the component renderer for sys_email and sys_email_draft tables.
 *
 * ```jsx
 * <sn-email-client-wrapper
 * 		table={..}
 * 		sys-id={..}
 * 		params={..}
 * />
 * ```
 *
 * @seismicElement sn-email-client-wrapper
 * @summary Component renderer for sys_email and sys_email_draft tables.
 *
 */
export const SnEmailClientWrapper = 'sn-email-client-wrapper';
createCustomElement(SnEmailClientWrapper, {
	renderer: {
		type: snabbdom
	},
	view,
	properties: {
		/**
		 * The table name of the record (either one of sys_email/sys_email_draft)
		 */
		table: {},
		/**
		 * The sysId of the email record
		 */
		sysId: {},
		/**
		 * The object containing email client config
		 */
		params: { default: {} },
		/**
		 * Tab id of the workspace tab in which the component is loaded
		 */
		tabId: { default: '' }
	}
});
