import { EMAIL_MINI_COMPOSER_ACTIONS } from './constants';
import { COMPOSITE_ACTIONS } from '../../constants/emailConstants';
import compositeActionHandlers from '../../commons/utils/compositeActionHandlers';

const handleEmailDraftInitialised = ({ dispatch, action: { payload } }) => {
	const { table, sysId, subject } = payload;
	dispatch(EMAIL_MINI_COMPOSER_ACTIONS.EMAIL_DRAFT_INITIALISED, {
		table: table,
		sysId: sysId,
		subject: subject
	});
};

const handleEmailDraftModified = ({ dispatch, action: { payload } }) => {
	const { sysId, table } = payload;
	dispatch(EMAIL_MINI_COMPOSER_ACTIONS.EMAIL_DRAFT_MODIFIED, {
		table: table,
		sysId: sysId
	});
};

const handleEmailDraftSaved = ({ dispatch, action: { payload } }) => {
	const { sysId, table } = payload;
	dispatch(EMAIL_MINI_COMPOSER_ACTIONS.EMAIL_DRAFT_SAVE_SUCCESS, {
		table: table,
		sysId: sysId
	});
};

const handleEmailDraftSend = ({ dispatch, action: { payload } }) => {
	const { sysId, table } = payload;
	dispatch(EMAIL_MINI_COMPOSER_ACTIONS.EMAIL_DRAFT_SEND_SUCCESS, {
		table: table,
		sysId: sysId
	});
};

const miniComposerPopOutHandler = ({ updateState }) =>
	updateState({
		popOutButtonClicked: true,
		initiateSave: true
	});

const handleAttachmentAddedAction = ({dispatch}) => {
	dispatch(EMAIL_MINI_COMPOSER_ACTIONS.ATTACHMENTS_ADDED);
}

export default {
	...compositeActionHandlers,
	[COMPOSITE_ACTIONS.EMAIL_DRAFT_INITIALISED]: {
		effect: handleEmailDraftInitialised,
		stopPropagation: true
	},
	[COMPOSITE_ACTIONS.EMAIL_DRAFT_MODIFIED]: {
		effect: handleEmailDraftModified,
		stopPropagation: true
	},
	[COMPOSITE_ACTIONS.EMAIL_SUCCESSFULLY_SAVED]: {
		effect: handleEmailDraftSaved,
		stopPropagation: true
	},
	[COMPOSITE_ACTIONS.EMAIL_SUCCESSFULLY_SEND]: {
		effect: handleEmailDraftSend,
		stopPropagation: true
	},
	[EMAIL_MINI_COMPOSER_ACTIONS.EMAIL_CLIENT_POP_OUT_BUTTON_CLICKED]: {
		effect: miniComposerPopOutHandler,
		stopPropagation: true
	},
	[COMPOSITE_ACTIONS.LOAD_DOCUMENT_VIEWER]: {
		effect: handleAttachmentAddedAction,
		stopPropagation: true
	}
};
