import { ATTACHMENT_CONSTANTS } from '../constants';
import { t } from 'sn-translate';
import '@servicenow/now-dropdown';


export const createAddAttachmentAction = (updateState, dispatch) => {
	const browseOptions = ATTACHMENT_CONSTANTS.ATTACHMENT_BROWSE_OPTIONS;
	return (
		<div className="ec-attachments-add-menu-container">
			<input
				type="file"
				id="ec-file-select"
				multiple
				on-click={e => {
					e.target.value = '';
				}}
				on-change={e => {
					dispatch(ATTACHMENT_CONSTANTS.ATTACH_FILES, {
						files: e.target.files
					});
				}}
				hook-insert={
					({elm}) => updateState({fileInputRef: elm})
				}
				hook-destroy={
					() => updateState({fileInputRef: null})
				}
			/>
			<div className="ec-add-files">
				<label htmlFor="#ec-add-file" className="ec-add-file-label">
					{t('Attachments:')}
				</label>
				<now-dropdown-custom-target
					items={browseOptions}
					id="ec-add-file"
					select="none"
					append-to-payload={{
						dispatchedEvent:
							ATTACHMENT_CONSTANTS.ATTACHMENT_BROWSE_ACTION_CLICKED,
					}}>
					<now-button-bare
						slot="trigger"
						variant="secondary"
						label={t('Add File')}
						tooltip-content={t('AttachfilestoEmail')}
						size="sm"
						icon-start="plus-fill"
					/>
				</now-dropdown-custom-target>
			</div>
		</div>
	);
};
