const GLIDE_EMAIL_QUERY = 'GlideEmail_Query';
const GLIDE_EMAIL_QUICK_MESSAGES = 'getQuickMessages';

export const EMAIL_CLIENT_QUICK_MESSAGE_QUERY = `
query($table: String!, $id: String!, $dirtyFields: [String!]) {
	${GLIDE_EMAIL_QUERY} {
		${GLIDE_EMAIL_QUICK_MESSAGES}(table: $table, id: $id, dirtyFields:  $dirtyFields) {
			sysId
			name
			body
		}
	}
}`;
