import { t } from 'sn-translate';
import { QUICK_MESSAGE_CONSTANTS } from '../constants';
import { handleInput, getCards } from './utils';
import '@servicenow/now-icon';
import '@servicenow/now-heading';

export default (state, { dispatch }) => {
	const { items, searchedItems, title, value, selectedItem } = state.properties;
	const cardItems = searchedItems ? searchedItems : items;

	return (
		<div
			className="quick-message-panel"
			role="complementary"
			id="quick-message-panel">
			<div className="quick-message-panel-header">
				<now-heading label={title} level="3" variant="header-secondary" />
				<div className="quick-message-panel-search">
					<div className="quick-message-panel-search-icon" tabindex="-1">
						<now-icon icon="magnifying-glass-outline" size="md" />
					</div>
					<input		//TODO: Change to now-input
						role="searchbox"
						placeholder={QUICK_MESSAGE_CONSTANTS.SEARCH_MESSAGES}
						aria-label={t('Type here to search for Quick Messages')}
						aria-controls="quick-message-card-list"
						value={value}
						className="quick-message-panel-search-input"
						on-input={e => handleInput(e, dispatch, items)}
					/>
				</div>
			</div>
			<div
				className="quick-message-panel-body"
				id="quick-message-card-list"
				aria-live="polite">
				{getCards(cardItems, value, selectedItem)}
			</div>
		</div>
	);
};
