import './ListView/listView';
import './DetailView/detailView';
import { QUICK_MESSAGE_CONSTANTS } from './constants';

export default state => {
	const { activeItems } = state.properties;
	const { searchedItems, searchString, selectedItem, detailView } = state;
	const items = searchedItems || activeItems;
	return (
		<div className="quick-message-body">
			{detailView ? (
				<sn-email-client-internal-quick-message-detail-view
					items={items}
					selectedItem={selectedItem}
				/>
			) : (
				<sn-email-client-internal-quick-message-list-view
					items={activeItems}
					searchedItems={searchedItems}
					value={searchString}
					title={QUICK_MESSAGE_CONSTANTS.TITLE}
					selectedItem={selectedItem}
				/>
			)}
		</div>
	);
};
