import { EMAIL_VIEWER_CONSTANTS } from '../constants';

const handleShowDetailsButtonPressed = ({ updateState, state }) => {
	const { showDetails } = state;
	updateState({ showDetails: !showDetails });
};
export default {
	[EMAIL_VIEWER_CONSTANTS.SHOW_DETAILS_BUTTON_PRESSED]: {
		effect: handleShowDetailsButtonPressed,
		stopPropagation: true
	}
};
