import { t } from 'sn-translate';
import { SUBJECT_CONSTANTS } from '../constants';
import '@servicenow/now-button';

export default (state, dispatch) => {
	const {subjectValue, important} = state;
	const importantAriaLabel =
		t('The email is marked as ') +
		(important ? t('important.') : t('not important.')) +
		t(' Click to toggle important flag');
	return (
		<div className="ec-subject-important ec-label-input-container">
			<label htmlFor="ec-subject-input" className="ec-field-label">
				{t('Subject:')}
			</label>
			<div className="ec-subject-important-container">
				<input
					className="ec-filled-input"
					id="ec-subject-input"
					type="text"
					maxlength="160"
					on-input={e =>
						dispatch(SUBJECT_CONSTANTS.EMAIL_SUBJECT_CHANGED, {
							value: e.target.value
						})
					}
					value={subjectValue}
				/>
				<now-button-stateful
					icon="flag-outline"
					config-role="button"
					config-aria={{
						'aria-label': importantAriaLabel,
						'aria-pressed': important
					}}
					size="md"
					tooltip-content={
						important ? t('Remove flag') : t('Flag as important')
					}
					selected={important}
					manage-selected
					variant="tertiary"
					append-to-payload={{
						dispatchedEvent: SUBJECT_CONSTANTS.EMAIL_IMPORTANT_FLAG_CHANGED
					}}
				/>
			</div>
		</div>
	);
};
