import { createElementFromNode } from '@servicenow/ui-renderer-snabbdom';

const convertStringToHtml = body => {
	if (!document || !document.createElement) {
		return;
	}
	const fragment = document.createElement('DIV');
	fragment.innerHTML = body;
	fragment.setAttribute('id', 'ec-viewer-body-value');
	return createElementFromNode(fragment);
};

export default body => {
	return <div className="ec-body-container">{convertStringToHtml(body)}</div>;
};
