import './typeahead/typeahead';
import { mapEndLabelsToDivs } from './typeahead/utils';
import { t } from 'sn-translate';

export default (state, dispatch) => {
	return (
		<div className="ec-recipient-picker">
			{/* *Recipient To Picker */}
			<div className="ec-recipient-picker-wrapper">
				<sn-email-client-internal-typeahead
					id="ec-recipient-picker-field-to"
					className="ec-recipient-picker-fields"
					label={t('To')}
					dataState="recipients_to"
					pillItems={state.recipients_to}
					availableItems={state.recipientSearchResults}
					opened={state.opened}
				/>
				{!state.showCcBcc ? mapEndLabelsToDivs(dispatch) : ''}
			</div>
			{/**Recipient cc picker */}
			{state.showCcBcc ? (
				<sn-email-client-internal-typeahead
					id="ec-recipient-picker-field-cc"
					className="ec-recipient-picker-fields"
					label={t('Cc')}
					dataState="recipients_cc"
					pillItems={state.recipients_cc}
					availableItems={state.recipientSearchResults}
					opened={state.opened}
					autofocus={state.autofocus}
				/>
			) : (
				''
			)}
			{/**Recipient bcc picker */}
			{state.showCcBcc ? (
				<sn-email-client-internal-typeahead
					id="ec-recipient-picker-field-bcc"
					className="ec-recipient-picker-fields"
					label={t('Bcc')}
					dataState="recipients_bcc"
					pillItems={state.recipients_bcc}
					availableItems={state.recipientSearchResults}
					opened={state.opened}
				/>
			) : (
				''
			)}
		</div>
	);
};
