import { DOC_VIEWER_STATES } from '../constants';
import { t } from 'sn-translate';
import { ATTACHMENT_PREVIEW_ERROR_MAP } from '../constants';
import { NOW_ACTIONS, ALERT_CONSTANTS } from '../../../constants/nowConstants';
 
export default (attachment, docViewerState, dispatch) => {
    const {sysId} = attachment;
    switch (docViewerState) {
        case DOC_VIEWER_STATES.LOADING: 
            return (
                <div className="doc-viewer-loader">
                    <span aria-live="assertive" className="loader-container">
                        <now-loader size="lg" label={t('Loading')} />
                    </span>
                </div>  
            )
        case DOC_VIEWER_STATES.READY:
            return (
                <div className="email-client-attachment-preview-overlay">
                    <sn-document-viewer
                        sys-id={sysId}
                        delete-hidden='true'
                    />
                </div>
            )
        case DOC_VIEWER_STATES.FAILED:
            dispatch(NOW_ACTIONS.ADD_TO_ALERT_LIST, {
                alerts: [
                  {
                    id: `now_email_alert_${Date.now()}`,
                    status: ALERT_CONSTANTS.STATUS.INFO,
                    icon: ALERT_CONSTANTS.ICONS.INFO,
                    content: ATTACHMENT_PREVIEW_ERROR_MAP.get(docViewerState),
                    action: { type: ALERT_CONSTANTS.ACTION.TYPES.DISMISS }
                  }
                ]
            });
        break;
    }   
}