import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './styles.scss';

/**
 * Internal quick message list view  component for email client
 *
 * ```jsx
 * <sn-email-client-internal-quick-message-list-view
 * 		searched-items={..}
 * 		items={..}
 * 		value={..}
 * 		title={..}
 * 		selected-item={..}
 * />
 * ```
 *
 * @seismicElement sn-email-client-internal-quick-message-list-view
 * @summary Internal quick message list view  component for email client
 *
 */

createCustomElement('sn-email-client-internal-quick-message-list-view', {
	renderer: {
		type: snabbdom
	},
	view,
	properties: {
		items: { default: [] },
		searchedItems: { default: null },
		value: { default: '', manageable: true, schema: { type: 'string' } },
		title: { default: '', manageable: true, schema: { type: 'string' } },
		selectedItem: { default: '' }
	},
	styles
});
