import { QUICK_MESSAGE_CONSTANTS } from '../constants';
import { convertMarkup, focusFirstChild } from '../utils';
import { debounce } from 'lodash';
import { t } from 'sn-translate';
import '@servicenow/now-card';

const hookInsert = (elm, selectedItem, index) => {
	if (selectedItem === index) {
		setTimeout(() => {
			focusFirstChild(elm);
		}, 100);
	}
};

const getCardList = (messages, selectedItem) =>
	messages.map((message, index) => {
		const title = message.name;
		const lines =
			title.length < QUICK_MESSAGE_CONSTANTS.MAX_CHAR_IN_CARD_HEADER_LINE
				? 1
				: 2;

		const screenReaderLabel =
			`${QUICK_MESSAGE_CONSTANTS.TITLE} ${title} ${index + 1} ` +
			t('out of') +
			` ${messages.length}`; //focused QM name and its position
		return (
			<div className="quick-message-panel-body-card-list" key={index}>
				<now-card
					size="lg"
					interaction="click"
					config-aria={{ 'aria-label': screenReaderLabel }}
					hook-insert={({ elm }) => hookInsert(elm, selectedItem, index)}
					append-to-payload={{
						dispatchedEvent: QUICK_MESSAGE_CONSTANTS.UPDATE_ITEM_STATE,
						selectedItem: index,
						detailView: true
					}}>
					<now-card-header
						heading={{
							label: t(`{0}`, title),
							size: 'sm',
							lines: lines,
							variant: 'primary'
						}}
						actions={[
							{
								id: 'insertAction',
								icon: 'document-plus-outline',
								label: t('Insert'),
								clickActionType: QUICK_MESSAGE_CONSTANTS.INSERT_BUTTON_CLICKED,
								message: message
							}
						]}
					/>
					<div>{convertMarkup(message.body, true)}</div>
				</now-card>
			</div>
		);
	});

const getEmptyView = searchString => {
	let label = QUICK_MESSAGE_CONSTANTS.NO_QUICK_MESSAGES_YET;
	let backgroundImageClass = 'no-match-icon';

	if (searchString) {
		label = QUICK_MESSAGE_CONSTANTS.NO_MATCHING_RESULT;
		backgroundImageClass = 'no-record-icon';
	}

	return (
		<div className="quick-message-panel-body-empty">
			<div
				className={`quick-message-panel-body-empty-${backgroundImageClass}`}
			/>
			<div className="quick-message-panel-body-empty-message">{label}</div>
		</div>
	);
};

const searchResults = (chars, items, dispatch) => {
	const filteredItems = items.filter(message =>
		new RegExp(chars, 'i').test(message.name)
	);
	dispatch(QUICK_MESSAGE_CONSTANTS.UPDATE_ITEM_STATE, {
		searchedItems: filteredItems,
		searchString: chars
	});
};

const debounceFn = debounce(
	searchResults,
	1000 //hack for VO + Safari as local search is too fast
);

export const getCards = (messages, value, selectedItem) =>
	messages.length ? getCardList(messages, selectedItem) : getEmptyView(value);

export const handleInput = (e, dispatch, items) => {
	const chars = e.target.value.trimStart();
	if (chars) {
		debounceFn(chars, items, dispatch);
	} else {
		debounceFn.cancel();
		dispatch(QUICK_MESSAGE_CONSTANTS.UPDATE_ITEM_STATE, {
			searchedItems: null,
			searchString: ''
		});
	}
};
