const GLIDE_EMAIL_MUTATION = 'GlideEmail_Mutation';
const GLIDE_EMAIL_CREATE_RESPONSE_DRAFT = 'createResponseDraft';

const EMAIL_CLIENT_CREATE_RESPONSE_DRAFT = `
mutation($emailRecordId: String!, $responseType: String!) {
	${GLIDE_EMAIL_MUTATION} {
		${GLIDE_EMAIL_CREATE_RESPONSE_DRAFT}(originalEmailId: $emailRecordId, responseType: $responseType) {
			sysId
			author
			subject
			targetTable
			targetRecord
			body
			important
			from
			fromChoices
			replyTo
			emails
			to {
				emailAddress
				displayName
				recipientSource
				recipientSourceTable
			}
			cc {
				emailAddress
				displayName
				recipientSource
				recipientSourceTable
			}
			bcc {
				emailAddress
				displayName
				recipientSource
				recipientSourceTable
            },
            attachments {
				attachmentId
				contentDisposition
				draftId
				sysId
				attachment {
					name
					size
					state
					sysId
					type
				}
			}
		}
	}
}`;

export const COMMON_EMAIL_MUTATIONS = Object.freeze({
	EMAIL_CLIENT_CREATE_RESPONSE_DRAFT
});
