import EmailFrom from './EmailFrom/EmailFrom';
import EmailReplyTo from './EmailReplyTo/EmailReplyTo';
import EmailRecipientPicker from './RecipientPicker/emailRecipientPicker';
import EmailSubject from './EmailSubject/EmailSubject';
import EmailEditor from './EmailEditor/EmailEditor';
import EmailAttachments from '../../commons/EmailAttachments/Attachments/Attachments';
import { createAttachmentDropOverlay } from './DragDrop/DragDrop';
import { ATTACHMENT_CONSTANTS } from '../../commons/EmailAttachments/constants';
import { COMPOSITE_ACTIONS } from '../../constants/emailConstants';
import { EMAIL_MINI_COMPOSER_ACTIONS } from '../now-email-client-mini-composer-connected/constants';
import {
	EMAIL_DRAFT_CONSTANTS,
	CONFIRMATION_MODAL_CONSTANTS
} from './constants';
import { t } from 'sn-translate';
import '@servicenow/now-loader';
import '@servicenow/now-modal';
import '@servicenow/now-button';
import '@servicenow/now-alert';

const modalActions = [
	{
		id: CONFIRMATION_MODAL_CONSTANTS.SEND,
		label: t('Yes, send'),
		variant: 'primary'
	},
	{
		id: CONFIRMATION_MODAL_CONSTANTS.DONT_SEND,
		label: t('No'),
		variant: 'secondary'
	}
];

export default (state, { dispatch, updateState }) => {
	const {
		status,
		showConfirmationModel,
		config: { configFetched },
		responseObjModified,
		properties: { showEmailDetails, tabComposerDetails }
	} = state;
	if (!configFetched || responseObjModified) {
		return (
			<div className="ec-loader">
				<span aria-live="assertive" className="ec-loader-container">
					<now-loader size="lg" label={t('Loading')} />
				</span>
			</div>
		);
	}
	const onDropHandler = (e, state, dispatch) => {
		const files = Array.from(e.dataTransfer.files);
		updateState({ showAttachmentDropOverlay: false });
		dispatch(ATTACHMENT_CONSTANTS.ATTACH_FILES, { files });
	};
	const onDrop = e => onDropHandler(e, state, dispatch);
	const fullScreenClass = document.body.classList.contains('mce-fullscreen')
		? 'mce-fullscreen'
		: '';
	return (
		<div className="ec-details-view">
			{status !== 'none' ? (
				<div className="ec-loader">
					<span aria-live="assertive" className="ec-loader-container">
						<now-loader size="lg" label={t(status)} />
					</span>
				</div>
			) : (
				''
			)}
			<div
				className={'ec-body ' + (status !== 'none' ? 'ec-body-noaction' : '')}
				role="form"
				on-drop={e => e.preventDefault()}
				on-dragover={e => e.preventDefault()}
				on-scroll={() => {
					if (state.recipientSearchResults) {
						updateState({ recipientSearchResults: [] }); //removing anchoring between dropdown and recipient input.
					}
				}}>
				<div className="ec-body-show-hide-fields">
					<now-button-bare
						id="ec-composer-details-show-hide-details"
						size="sm"
						label={
							showEmailDetails
								? t('Hide email details')
								: t('Show email details')
						}
						variant="secondary"
						config-aria={{
							'aria-label': 'Show/hide details button'
						}}
						append-to-payload={{
							dispatchedEvent: COMPOSITE_ACTIONS.EMAIL_SHOW_HIDE_BUTTON_CLICKED
						}}
					/>
					{!tabComposerDetails ? (
						<now-button-iconic
							icon="tabset-outline"
							id="ec-composer-details-pop-out-button"
							tooltip-content={t("Pop out email draft to new workspace tab")}
							size="md"
							variant="tertiary"
							bare={true}
							config-aria={{
								'aria-label': 'Pop out email draft to new workspace tab'
							}}
							append-to-payload={{
								dispatchedEvent:
									EMAIL_MINI_COMPOSER_ACTIONS.EMAIL_CLIENT_POP_OUT_BUTTON_CLICKED
							}}
						/>
					) : (
						''
					)}
				</div>
				{showEmailDetails ? (
					<div
						className="ec-attachment-drop-container"
						on-dragover={() => {
							if (!state.showAttachmentDropOverlay) {
								updateState({
									showAttachmentDropOverlay: true,
									showEditorDropOverlay: false,
									browseTypePannelopened: false
								});
							}
						}}>
						<div className="ec-body-input-container">
							{state.config.displayFrom ? EmailFrom(state) : ''}
							{state.config.displayReplyTo
								? EmailReplyTo(state, updateState, dispatch)
								: ''}
							{EmailRecipientPicker(state, dispatch, updateState)}
							{EmailSubject(state, dispatch, updateState)}
						</div>
						<div className="ec-body-attachments-container">
							{EmailAttachments(state, dispatch, updateState)}
						</div>
						{state.showAttachmentDropOverlay
							? createAttachmentDropOverlay(
									state,
									updateState,
									dispatch,
									onDrop
							  )
							: ''}
					</div>
				) : (
					''
				)}
				<div className={`ec-body-container ${fullScreenClass}`}>
					{EmailEditor(state, dispatch, updateState)}
				</div>
				{showConfirmationModel ? (
					<now-modal
						id="ec-send-confirmation-model"
						content={t('There is no subject or message yet.')}
						header-label={t('Do you want to send anyway?')}
						size="md"
						opened
						manage-opened
						footer-actions={modalActions}
						append-to-payload={{
							dispatchedEvent:
								EMAIL_DRAFT_CONSTANTS.EMAIL_CLIENT_MODAL_CONFIRMATION_BUTTON_CLICKED
						}}
					/>
				) : (
					''
				)}
			</div>
		</div>
	);
};
