import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './styles.scss';
import compositeInitialState from '../../commons/utils/compositeInitialState';
import actionHandlers from './actionHandlers';
import { EMAIL_MINI_COMPOSER_ACTIONS } from './constants';

/**
 * Component used to render the mini email composer.
 *
 * ```jsx
 * <now-email-client-mini-composer-connected
 * 		sys-id={..}
 * 		target-table={..}
 * 		target-record={..}
 * 		reply-type={..}
 * 		reply-id={..}
 * 		show-email-details={..}
 * />
 * ```
 *
 * @seismicElement now-email-client-mini-composer-connected
 * @summary Component used to render the mini email composer.
 *
 */
export const NowEmailClientMiniComposer =
	'now-email-client-mini-composer-connected';
createCustomElement(NowEmailClientMiniComposer, {
	renderer: {
		type: snabbdom
	},
	view,
	styles,
	initialState: {
		...compositeInitialState,
		popOutButtonClicked: false
	},
	actionHandlers,
	properties: {
		/**
		 * The sysId of the email_draft record
		 * @type {sysId}
		 */
		sysId: {},
		/**
		 * Table name of the record againnst which Email Client
		 * needs to be launched
		 * @type {tableName}
		 */
		targetTable: { default: '' },
		/**
		 * Record sysId against which Email Client needs to be launched
		 * @type {sysId}
		 */
		targetRecord: { default: '' },
		/**
		 * Dirty fields in the form before the email client gets launched.
		 */
		dirtyFields: { default: [] },
		/**
		 * The reply type in case its a responce draft - reply, replyall or forward
		 * @type {string}
		 */
		replyType: { default: '' },
		/**
		 * The sys_id of the email to reply to in case its a responce draft
		 * @type {sysId}
		 */
		replyId: { default: '' },
		/**
		 * Flag to show/hide email details - false by default
		 * @type {boolean}
		 */
		showEmailDetails: { default: false }
	},
	dispatches: {
		/**
		 * Dispatched when the email draft has been initialised and rendered
		 * @type {{table: string, sysId: string, subject: string}}
		 */
		[EMAIL_MINI_COMPOSER_ACTIONS.EMAIL_DRAFT_INITIALISED]: {},
		/**
		 * Dispatched when any field of the email draft has been modified
		 * @type {EmailDraftPayload}
		 */
		[EMAIL_MINI_COMPOSER_ACTIONS.EMAIL_DRAFT_MODIFIED]: {},
		/**
		 * Dispatched when the email draft has been succesfully saved
		 * @type {EmailDraftPayload}
		 */
		[EMAIL_MINI_COMPOSER_ACTIONS.EMAIL_DRAFT_SAVE_SUCCESS]: {},
		/**
		 * Dispatched when the email draft has been succesfully send
		 * @type {EmailDraftPayload}
		 */
		[EMAIL_MINI_COMPOSER_ACTIONS.EMAIL_DRAFT_SEND_SUCCESS]: {},
		/**
		 * Dispatched when the email draft has to be pulled into a new workspace tab
		 */
		[EMAIL_MINI_COMPOSER_ACTIONS.EMAIL_CLIENT_POP_OUT_BUTTON_CLICKED]: {},
		/**
		 * Dipsatched when attachments are present inside the details component. For document viewer load usecase
		 */
		[EMAIL_MINI_COMPOSER_ACTIONS.ATTACHMENTS_ADDED]: {},
		/**
		 * Dispatch when the pop out button is clicked and the save succeeds
		 */
		[EMAIL_MINI_COMPOSER_ACTIONS.PREVIEW_RECORD]: {}
	}
});
