import '../sn-email-client-internal-header';
import { t } from 'sn-translate';
import EmailDetails from './EmailDetails/EmailDetails';
import EmailBody from './EmailBody/EmailBody';
import {
	createAttachmentTiles,
	getAttachmentTileCount,
	checkEmailTypeAndSentActions
} from './utils';
import isEmpty from 'lodash/isEmpty';
import '@servicenow/now-loader';
import '@servicenow/now-alert';
import PreviewAttachment from '../../commons/EmailAttachments/AttachmentPreview/AttachmentPreview';
import { DOC_VIEWER_STATES } from '../../commons/EmailAttachments/constants';

export default (state, { updateState, dispatch }) => {
	const { emailDataObject, showDetails, alertList = [], attachmentToPreview, docViewerState } = state;
	if (!emailDataObject) {
		return (
			<div className="ec-loader">
				<span aria-live="assertive" className="ec-loader-container">
					<now-loader size="lg" label={t('Loading')} />
				</span>
			</div>
		);
	}
	const {
		subject,
		to,
		cc,
		bcc,
		body,
		userInfo,
		type,
		createdOn,
		important,
		attachments = []
	} = emailDataObject.getValues();
	const attachmentTileCount = getAttachmentTileCount(attachments);
	const hasAlertList = alertList && !isEmpty(alertList);
	const isDocViewerLoading = attachmentToPreview && (docViewerState === DOC_VIEWER_STATES.LOADING);
	const docViewerLoaderClassName = isDocViewerLoading ? 'doc-viewer-loading' : '';
	return (
		<div className="ec-viewer" role="main">
			{attachmentToPreview ? PreviewAttachment(attachmentToPreview, docViewerState, dispatch) : ''}
			<sn-email-client-internal-header
				className={`${docViewerLoaderClassName}`}
				headingValue={subject}
				headerButtons={checkEmailTypeAndSentActions(type)}
			/>
			<hr className={`ec-divider ${docViewerLoaderClassName}`} />
			<div className={`ec-viewer-details ${docViewerLoaderClassName}`}>
				{EmailDetails({
					userInfo,
					to,
					cc,
					bcc,
					subject,
					showDetails,
					createdOn,
					important
				})}
			</div>
			{attachmentTileCount.length ? (
				<div className={`ec-viewer-attachments ${docViewerLoaderClassName}`}>
					<div className="ec-viewer-attachments-label">{t('Attachments:')}</div>
					<div className="ec-viewer-attachments-list">
						{createAttachmentTiles(attachmentTileCount, state, updateState)}
					</div>
				</div>
			) : (
				''
			)}
			<hr className={`ec-divider ${docViewerLoaderClassName}`} />
			<div className={`ec-viewer-body ${docViewerLoaderClassName}`}>
				{EmailBody(body)}
			</div>
			{hasAlertList ? (
				<div className={`ec-alerts-container ${docViewerLoaderClassName}`}>
					<span aria-live="assertive">
						<now-alert-list
							items={alertList}
							manage-items={true}
							className="ec-alerts"
						/>
					</span>
				</div>
			) : (
				''
			)}
		</div>
	);
};
