/* eslint-disable no-console */
import tinymceStyles from './tinymce.css';
import { uniqBy } from 'lodash';
import 'sn-tinymce';
import { isDragDropEnabled } from '../constants';

//Tinymce Inits
window.tinymce.baseURL =
	'/scripts/tinymce_default/node_modules/sn-tinymce/js/tinymce';

const deleteImgs = (imgs = [], ed, isExtracted = false) => {
	const deleteImgs = [];
	imgs.forEach(img => {
		if (!hasImgDuplicates(img, ed, isExtracted)) {
			deleteImgs.push(img);
		}
	});
	if (deleteImgs.length) {
		const host = ed.targetElm.getRootNode().host;
		const sysIds = deleteImgs.map(img => {
			return img.dataset.sysId || img.src.split('?sys_id=').pop();
		});
		host.dispatchEvent(new CustomEvent('image-delete', { detail: { sysIds } }));
	}
};

const hasImgDuplicates = (img, ed, isExtracted = false) => {
	const body = ed.getBody();
	const sysId = img.dataset.sysId;
	const imgCopies = body.querySelectorAll(`img[data-sys-id*='${sysId}']`);
	const imgCopyThreshold = isExtracted ? 0 : 1;
	return imgCopies.length > imgCopyThreshold;
};

const deleteSelectedImgs = ed => {
	const img = ed.selection.getNode();
	if (img && img.nodeName == 'IMG') {
		deleteImgs([img], ed, false);
	} else if (!ed.selection.isCollapsed()) {
		let rng = ed.selection.getRng();
		rng = rng.cloneRange();
		const fragment = rng.extractContents();
		let imgs = fragment.querySelectorAll('img[data-sys-id]');
		imgs = uniqBy([...imgs], function(img) {
			return img.dataset.sysId;
		});
		deleteImgs(imgs, ed, true);
	}
};

window.tinymce.on('addeditor', e => {
	if (
		e.editor &&
		e.editor.targetElm &&
		e.editor.targetElm.getRootNode().host &&
		e.editor.targetElm.getRootNode().host.name == 'sn-email-client-editor'
	) {
		const host = e.editor.targetElm.getRootNode().host;
		const ed = e.editor;
		ed.on('init', function(e) {
			//Editor Ready
			const root = e.target.container.getRootNode(),
				style = document.createElement('style');
			style.setAttribute('type', 'text/css');
			style.innerHTML = tinymceStyles;
			root.insertBefore(style, root.querySelector('.sn-editor'));

			//save editor reference in state
			host.dispatchEvent(new CustomEvent('editorRef', { detail: ed }));
		})
			//bring drop overlay on drag start
			.on('dragover', () => {
				host.dispatchEvent(new CustomEvent('dragover'));
			})
			.on('drop', e => {
				if (!isDragDropEnabled) {
					e.preventDefault();
					e.stopPropagation();
					return false;
				}
			})
			.on('keydown', e => {
				if ([8, 46, 13, 32].indexOf(e.keyCode) >= 0) {
					deleteSelectedImgs(ed);
				}
			});
	}
});
