import { QUICK_MESSAGE_CONSTANTS } from '../constants';
import { convertMarkup } from '../utils';
import { hookInsert, handleScroll } from './utils';
import { createRef } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-button';

const headerRef = createRef();
const bodyRef = createRef();

export default state => {
	const { items, selectedItem } = state.properties;
	const item = items[selectedItem];
	if (bodyRef.current) {
		bodyRef.current.scrollTop = 0;
	} //reset scroll on re-render

	return (
		<div className="quick-message-detail" role="complementary">
			<div
				className="quick-message-detail-header"
				hook-insert={hookInsert}
				ref={headerRef}>
				<div className="quick-message-detail-header-actions">
					<div className="quick-message-detail-header-actions-back">
						<now-button-iconic
							bare
							hide-padding
							config-aria={{
								'aria-label': QUICK_MESSAGE_CONSTANTS.BACK_ARIA_MESSAGE
							}}
							id="quick-message-detail-back-button"
							icon="chevron-left-outline"
							size="lg"
							tooltip-content={QUICK_MESSAGE_CONSTANTS.BACK_TOOL_TIP}
							variant="tertiary"
							append-to-payload={{
								dispatchedEvent: QUICK_MESSAGE_CONSTANTS.UPDATE_ITEM_STATE,
								detailView: false
							}}
						/>
					</div>
					<div className="quick-message-detail-header-actions-insert">
						<now-button-bare
							hide-padding
							config-aria={{
								'aria-label': QUICK_MESSAGE_CONSTANTS.INSERT_ARIA_MESSAGE
							}}
							icon-start="document-plus-outline"
							variant="secondary"
							size="sm"
							id="quick-message-detail-insert-button"
							label={QUICK_MESSAGE_CONSTANTS.INSERT}
							tooltip-content={QUICK_MESSAGE_CONSTANTS.INSERT}
							append-to-payload={{
								dispatchedEvent: QUICK_MESSAGE_CONSTANTS.INSERT_BUTTON_CLICKED,
								message: item
							}}
						/>
					</div>
				</div>
			</div>
			<div
				className="quick-message-detail-body"
				aria-live="polite"
				ref={bodyRef}
				on-scroll={({ currentTarget }) =>
					handleScroll(currentTarget, headerRef.current)
				}>
				<div className="quick-message-detail-body-title">{item.name}</div>
				<div className="quick-message-detail-body-content">
					{convertMarkup(item.body)}
				</div>
			</div>
			<div className="quick-message-detail-body-footer">
				<div className="quick-message-detail-body-footer-actions">
					<now-button-iconic
						bare
						disabled={!selectedItem}
						icon="arrow-left-fill"
						label=""
						size="md"
						id="quick-message-detail-previous-button"
						tooltip-content={QUICK_MESSAGE_CONSTANTS.PREVIOUS}
						configAria={{
							'aria-label': QUICK_MESSAGE_CONSTANTS.PREVIOUS
						}}
						variant="tertiary"
						append-to-payload={{
							dispatchedEvent: QUICK_MESSAGE_CONSTANTS.UPDATE_ITEM_STATE,
							selectedItem: selectedItem - 1
						}}
					/>
					<now-button-iconic
						bare
						disabled={selectedItem + 1 === items.length}
						icon="arrow-right-fill"
						label=""
						size="md"
						id="quick-message-detail-next-button"
						tooltip-content={QUICK_MESSAGE_CONSTANTS.NEXT}
						configAria={{
							'aria-label': QUICK_MESSAGE_CONSTANTS.NEXT
						}}
						variant="tertiary"
						append-to-payload={{
							dispatchedEvent: QUICK_MESSAGE_CONSTANTS.UPDATE_ITEM_STATE,
							selectedItem: selectedItem + 1
						}}
					/>
				</div>
			</div>
		</div>
	);
};
