import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import actionHandlers from './actionHandlers';
import initialState from './initialState';
import styles from './styles.scss';
import { QUICK_MESSAGE_INSERT } from './constants';

/**
 * An email side panel that enables users, such as workspace agents, to quickly send preconfigured messages in emails.
 *
 * ```jsx
 * <now-email-client-quick-messages
 * 		active-items={..}
 * />
 * ```
 *
 * @seismicElement now-email-client-quick-messages
 * @summary Lets users send preconfigured messages and responses through an email client contextual side panel. 
 *
 */
export const NowEmailClientQuickMessages = 'now-email-client-quick-messages';
createCustomElement(NowEmailClientQuickMessages, {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		/**
		 * List of quick messages to display. Messages are indexed so that the user can search for a specific message.
		 * @type {Array<QuickMessageItems>}
		 */
		activeItems: { default: [] }
	},
	initialState,
	actionHandlers,
	styles,
	dispatches: {
		/**
		 * Dispatched when the insert button is clicked on a quick message card.
		 * @type {QuickMessageDetails}
		 */
		[QUICK_MESSAGE_INSERT]: {}
	}
});
