/* eslint-disable no-console */
import { QUICK_MESSAGE_CONSTANTS, QUICK_MESSAGE_INSERT } from './constants';
import { NOW_ACTIONS } from '../../constants/nowConstants';
import { isInsert } from './constants';

const handleNowActions = ({ action, dispatch }) => {
	let { payload } = action;
	if (payload.dispatchedEvent) {
		dispatch(payload.dispatchedEvent, {
			...payload,
			nowAction: action
		});
	}
};

const handleInsertClick = ({ action: { payload }, dispatch }) => {
	const message = payload.action ? payload.action.message : payload.message;
	dispatch(QUICK_MESSAGE_INSERT, {
		message,
		isInsert
	});
};

const handleItemsState = ({ action: { payload }, updateState }) => {
	updateState({ ...payload });
};

export default {
	[NOW_ACTIONS.NOW_BUTTON_BARE_CLICKED]: {
		effect: handleNowActions,
		stopPropagation: true
	},
	[NOW_ACTIONS.NOW_BUTTON_ICONIC_CLICKED]: {
		effect: handleNowActions,
		stopPropagation: true
	},
	[NOW_ACTIONS.NOW_CARD_CLICKED]: {
		effect: handleNowActions,
		stopPropagation: true
	},
	[QUICK_MESSAGE_CONSTANTS.UPDATE_ITEM_STATE]: {
		effect: handleItemsState,
		stopPropagation: true
	},
	[QUICK_MESSAGE_CONSTANTS.INSERT_BUTTON_CLICKED]: {
		effect: handleInsertClick,
		stopPropagation: true
	}
};
