/* eslint-disable no-console */
import { EMAIL_RENDERING_STATUS } from '../constants';
import { HEADER_CONSTANTS } from '../constants';
import { ALERT_CONSTANTS, NOW_ACTIONS } from '../../../constants/nowConstants';
import {
	consolidateRecipients,
	validateAttachments,
	validateRecipients,
	checkRecipientToMandatory
} from './utils';
import { t } from 'sn-translate';
import { isEmpty } from 'lodash';
import { COMPOSITE_ACTIONS } from '../../../constants/emailConstants';

const dispatchRecipientAlerts = (dispatch, message) => {
	dispatch(NOW_ACTIONS.ADD_TO_ALERT_LIST, {
		alerts: [
			{
				id: `now_email_alert_${Date.now()}`,
				status: ALERT_CONSTANTS.STATUS.CRITICAL,
				icon: ALERT_CONSTANTS.ICONS.CRITICAL,
				content: message,
				action: { type: ALERT_CONSTANTS.ACTION.TYPES.DISMISS }
			}
		]
	});
	dispatch(COMPOSITE_ACTIONS.EMAIL_DRAFT_RECORD_SEND_CANCELED);
}

const handleSendButtonPressed = ({ state, dispatch, updateState }) => {
	const {
		recipients_to,
		recipients_cc,
		recipients_bcc,
		attachments,
		config : { toOptional }
	} = state;
	const { table, sysId } = state.targetRecord;
	if (checkRecipientToMandatory(recipients_to, toOptional)) {	//If To field is mandatory check for empty To
		dispatchRecipientAlerts(dispatch, t('The recipient To field is required.'));
		return;
	}
	else if (toOptional && 
		(isEmpty(recipients_to) && isEmpty(recipients_cc) && isEmpty(recipients_bcc))) {	//If To field is not mandatory check for at least one recipient.
			dispatchRecipientAlerts(dispatch, t('At least one recipient is required.'));
			return;
		}
	const isAttachmentsUploading = validateAttachments(
		attachments,
		dispatch
	);
	const isRecipientToValid = validateRecipients(recipients_to, dispatch);
	const isRecipientCcValid = validateRecipients(recipients_cc, dispatch);
	const isRecipientBccValid = validateRecipients(recipients_bcc, dispatch);
	const isRecipientsValid =
		isRecipientToValid && isRecipientCcValid && isRecipientBccValid;
	if (isAttachmentsUploading || !isRecipientsValid) {
		dispatch(COMPOSITE_ACTIONS.EMAIL_DRAFT_RECORD_SEND_CANCELED);
		return;
	}
	const { subjectValue } = state;
	if (isEmpty(subjectValue) || isEmpty(state.editorValue)) {
		updateState({ showConfirmationModel: true });
		return;
	}
	const to = consolidateRecipients(recipients_to);
	const cc = consolidateRecipients(recipients_cc);
	const bcc = consolidateRecipients(recipients_bcc);
	dispatch(HEADER_CONSTANTS.EMAIL_CLIENT_SEND_DRAFT, {
		draftRecord: {
			sysId: state.emailRecord.sysId,
			author: state.author,
			subject: state.subjectValue,
			body: state.editorValue,
			replyTo: state.replyToValue,
			from: state.fromValue,
			contentType: 'text/html',
			important: state.important,
			to: to,
			cc: cc,
			bcc: bcc,
			targetTable: table,
			targetRecord: sysId
		}
	});
	updateState({
		status: EMAIL_RENDERING_STATUS.SENDING
	});
};

const handleSaveButtonPressed = ({ state, dispatch, updateState }) => {
	const {
		recipients_to,
		recipients_cc,
		recipients_bcc,
		attachments
	} = state;
	const { table, sysId } = state.targetRecord;
	const isAttachmentsUploading = validateAttachments(
		attachments,
		dispatch
	);
	if (isAttachmentsUploading) {
		dispatch(COMPOSITE_ACTIONS.EMAIL_DRAFT_RECORD_SAVE_CANCELED);
		return;
	}
	const to = consolidateRecipients(recipients_to);
	const cc = consolidateRecipients(recipients_cc);
	const bcc = consolidateRecipients(recipients_bcc);
	dispatch(HEADER_CONSTANTS.EMAIL_CLIENT_UPDATE_DRAFT, {
		draftRecord: {
			sysId: state.emailRecord.sysId,
			author: state.author,
			subject: state.subjectValue,
			body: state.editorValue,
			replyTo: state.replyToValue,
			important: state.important,
			from: state.fromValue,
			contentType: 'text/html',
			to: to,
			cc: cc,
			bcc: bcc,
			targetTable: table,
			targetRecord: sysId
		}
	});
	updateState({
		status: EMAIL_RENDERING_STATUS.SAVING
	});
};

export default {
	[HEADER_CONSTANTS.EMAIL_HEADER_SEND_BUTTON_CLICKED]: {
		effect: handleSendButtonPressed,
		stopPropagation: true
	},
	[HEADER_CONSTANTS.EMAIL_HEADER_SAVE_BUTTON_CLICKED]: {
		effect: handleSaveButtonPressed,
		stopPropagation: true
	}
};
