import '../sn-email-client-internal-header';
import { HEADER_ACTION_BUTTONS } from './constants';
import '../now-email-client-composer-details';
import isEmpty from 'lodash/isEmpty';
import '@servicenow/now-alert';

const getValidHeaderButtons = () => {
	return [HEADER_ACTION_BUTTONS.SAVE_BUTTON, HEADER_ACTION_BUTTONS.SEND_BUTTON];
};

const checkComponentLoad = recordId => (recordId ? true : false);

export default state => {
	const {
		properties: {
			targetTable,
			targetRecord,
			dirtyFields,
			sysId,
			replyType,
			replyId,
			showEmailDetails
		}
	} = state;
	const { initiateSave, initiateSend, recordId, alertList = [] } = state;
	const hasAlertList = alertList && !isEmpty(alertList);
	return (
		<div className="ec-mini-email-composer">
			<div className="ec-mini-email-composer-details-container">
				<now-email-client-composer-details
					target-table={targetTable}
					target-record={targetRecord}
					dirty-fields={dirtyFields}
					draft-record-id={sysId}
					response-obj={{replyType, replyId}}
					send-draft={initiateSend}
					save-draft={initiateSave}
					show-email-details={showEmailDetails}
				/>
			</div>
			{checkComponentLoad(recordId) ? (
				<div className="ec-mini-email-composer-header-container">
					<sn-email-client-internal-header
						heading-value={''}
						header-buttons={getValidHeaderButtons()}
						padding="small"
					/>
				</div>
			) : ''}
			{hasAlertList ? (
					<div className='ec-alerts-container '>
						<span aria-live="assertive">
							<now-alert-list
								items={alertList}
								manage-items={true}
								className="ec-alerts"
							/>
						</span>
					</div>
				) : ''
			}
		</div>
	);
};
