import { ATTACHMENT_CARDS_STATUS } from './constants';

export const isOverflow = cardsEl => {
	const WIDTH_OF_ONE_ATTACHMENT_CARD = 224;			//Unfortunate; Sigh.
	const WIDTH_OF_CONTAINER = cardsEl.clientWidth;
	const numberOfLinesRequired = (cardsEl.childElementCount * WIDTH_OF_ONE_ATTACHMENT_CARD)/ WIDTH_OF_CONTAINER;
	return (numberOfLinesRequired > 2)
};

export const handleExpandCollapse = (
	cardsEl,
	hasMore,
	hasLess,
	updateState
) => {
	if (cardsEl) {
		const overflow = isOverflow(cardsEl);
		if (overflow) {
			if (hasMore || (!hasMore && !hasLess)) {
				collapseCards(updateState);
			} else {
				expandCards(updateState);
			}
		} else {
			normalCards(updateState);
		}
	}
};

export const collapseCards = updateState => {
	updateState({
		hasMore: true,
		hasLess: false,
		cardsStatus: ATTACHMENT_CARDS_STATUS.COLLALPSED
	});
};

export const expandCards = updateState => {
	updateState({
		hasMore: false,
		hasLess: true,
		cardsStatus: ATTACHMENT_CARDS_STATUS.EXPANDED
	});
};

export const normalCards = updateState => {
	updateState({
		hasMore: false,
		hasLess: false,
		cardsStatus: ATTACHMENT_CARDS_STATUS.NORMAL
	});
};

export const getSelectedAttachmentCount = attachments => {
	let count = 0;
	attachments.forEach(item => {
		if (item.selected) {
			count++;
		}
	});
	return count++;
};
