import resources from '../schemas/restQueries';
import { handleExpandCollapse } from '../utils';
import {
	ATTACHMENT_MUTATIONS,
	ATTACHMENTS_QUERY
} from '../schemas/datasources';
import { showAlert } from '../AttachmentErrors/AttachmentErrorHandler';
import { get, partition } from 'lodash';
import { MAX_ATTACHMENT_SIZE } from '../constants';
import { ERROR_REASONS } from '../AttachmentErrors/AttachmentErrorMap';

import {
	ATTACHMENT_CONSTANTS,
	ATTACHMENT_CARD_ACTIONS,
	ATTACHMENT_CONTENT_DISPOSITION,
	ATTACHMENT_QUERY_CONSTANTS
} from '../constants';
import { NOW_ACTIONS } from '../../../constants/nowConstants';
import { ErrorMap } from '../AttachmentErrors/AttachmentErrorMap';
import { COMPOSITE_ACTIONS } from '../../../constants/emailConstants';

const onBrowseButtonClick = ({ action, updateState }) => {
	updateState({
		...action.payload
	});
};

const onBrowseTypeSelect = ({ action: { payload }, state, dispatch }) => {
	if (
		payload.nowAction.type ===
		NOW_ACTIONS.NOW_DROPDOWN_CUSTOM_TARGET_ITEM_CLICKED
	) {
		const {item} = payload;
		const {
			targetRecord: { sysId, table },
			fileInputRef
		} = state;

		if (item.id === ATTACHMENT_CONSTANTS.BROWSE_FROM_COMPUTER) {
			fileInputRef.focus();
			fileInputRef.click();
		} else if (item.id === ATTACHMENT_CONSTANTS.BROWSE_FROM_RECORD) {
			dispatch(ATTACHMENT_CONSTANTS.GET_ATTACHMENTS_FROM_RECORD, {
				sysId,
				table
			});
		}
	}
};

export const getTruncatedFileName = fileName => {
	const limit = ATTACHMENT_CONSTANTS.FILE_NAME_CHAR_LIMIT;
	if (fileName.includes('.')) {
		var words = fileName.split('.');
		var extention = words.pop();
		var relativeLimit = limit - extention.length - 1;
		var name = words.join('.').substring(0, relativeLimit);
		return name + '.' + extention;
	} else {
		return fileName.substring(0, limit - 1);
	}
};

const attachFiles = ({
	state,
	action: {
		payload: { files }
	},
	dispatch,
	updateState
}) => {
	const data = new FormData();
	data.append(ATTACHMENT_QUERY_CONSTANTS.DRAFT_ID, state.emailRecord.sysId);
	data.append(
		ATTACHMENT_QUERY_CONSTANTS.CONTENT_DISPOSITION,
		ATTACHMENT_CONTENT_DISPOSITION.attachment
	);
	files = Array.from(files).map(file => {
		data.append(ATTACHMENT_QUERY_CONSTANTS.FILE, file);
		return {
			name: getTruncatedFileName(file.name),
			type: file.type,
			uploading: true,
			size: file.size,
			isSystemFile: true
		};
	});
	updateState({
		path: 'attachments',
		value: files,
		operation: 'concat'
	});
	dispatch(ATTACHMENT_CONSTANTS.FILES_UPLOADED, { data });
};

const uploadFiles = createHttpEffect(resources.UPLOAD_FILE.url, {
	...resources.UPLOAD_FILE.options,
	successActionType: ATTACHMENT_CONSTANTS.FILES_UPLOAD_SUCCEEDED,
	errorActionType: ATTACHMENT_CONSTANTS.FILES_UPLOAD_FAILED
});

const onFileUploadSuccess = ({
	action: { payload },
	updateState,
	dispatch,
	state
}) => {
	const { config: { docViewerEnabled }, attachments } = state;
	const uploaded = payload.result.uploaded.map(file => {
		return {
			...file.attachment,
			draftId: file.draftId,
			attachmentId: file.sysId
		};
	});
	const failed = payload.result.failed;
	const uploadedAttachments = attachments.filter(			//Remove failed attachments from list
		attachment => {
			const { sysId, name } = attachment;
			return sysId ? true : !(failed.findIndex(f => f.fileName === name) > -1);		//Check for sysId since the same attachment can be added twice.
		}
	).map(
		attachment => {
			const { sysId, name } = attachment;
			const file =
				uploaded.find(f => (sysId ? sysId === f.sysId : name === f.name)) || {};
			return { 
				...attachment,
				...file,
				uploading: false
			};
		}
	);
	updateState({
		attachments: uploadedAttachments
	});

	dispatch(COMPOSITE_ACTIONS.LOAD_DOCUMENT_VIEWER, {
		docViewerEnabled
	});

	//Alert users regarding the failed attachement
	if (failed.length > 0) {
		const alerts = failed.map(f => {
			const reason = f.description || '';
			const defaultAlert = ErrorMap['DEFAULT_ERROR'];
			if (ErrorMap.ATTACHMENTS_LIMIT.has(reason)) {
				const limitError = ErrorMap.ATTACHMENTS_LIMIT.get(reason);
				return { ...defaultAlert, ...limitError };
			} else if (reason === ERROR_REASONS.SIZE_LIMIT_ERROR) {
				const sizeLimitError = ErrorMap['ATTACHMENT_UPLOAD_SIZE_ERROR'](
					f.fileName,
					MAX_ATTACHMENT_SIZE
				);
				return { ...defaultAlert, ...sizeLimitError };
			} else if (reason.includes('Invalid file type')) {
				const invaliFileTypeError = ErrorMap[
					'ATTACHMENT_UPLOAD_INVALID_FILE_ERROR'
				](f.fileName);
				return { ...defaultAlert, ...invaliFileTypeError };
			} else {
				const defaultUploadError = ErrorMap['ATTACHMENT_UPLOAD_ERROR'](
					f.fileName
				);
				return { ...defaultAlert, ...defaultUploadError };
			}
		});
		dispatch(NOW_ACTIONS.ADD_TO_ALERT_LIST, { alerts });
	}
};

const onFileUploadFail = ({ action, dispatch, updateState, state }) => {
	const formData = get(action, 'meta.request.data');
	const failedFiles = formData.getAll(ATTACHMENT_QUERY_CONSTANTS.FILE) || [];
	let { attachments } = state;
	updateState({
		attachments: attachments.filter(attachment => 
			attachment.uploading ? !failedFiles.find(f => f.name === attachment.name) : true	
		)
	});

	const alerts = failedFiles.map(f => {
		const defaultAlert = ErrorMap['DEFAULT_ERROR'];
		const message = get(action, 'payload.data.message', '');
		const nwError = get(action, 'payload.message', '');
		const serverError = get(action, 'payload.data.error');
		let content;
		if (nwError === 'Network Error' && f.type === '') {
			content = ErrorMap['ATTACHMENT_UPLOAD_FOLDER_ERROR'](f.name);
		} else if (nwError === 'Network Error') {
			content = ErrorMap['ATTACHMENT_UPLOAD_NETWORK_ERROR'](f.name);
		} else if (serverError === 'Request Entity Too Large') {
			content = ErrorMap['ATTACHMENT_UPLOAD_SIZE_ERROR'](
				f.name,
				message.split(' ').pop()
			);
		} else {
			content = ErrorMap['ATTACHMENT_UPLOAD_ERROR'](f.name);
		}
		return { ...defaultAlert, ...content };
	});
	dispatch(NOW_ACTIONS.ADD_TO_ALERT_LIST, { alerts });
};

export const onComponentRender = ({
	action: {
		payload: { host }
	},
	state: { hasMore, hasLess },
	updateState
}) => {
	const cardsEl = host.shadowRoot.querySelector('.ec-attachments-cards');
	handleExpandCollapse(cardsEl, hasMore, hasLess, updateState);

	window.addEventListener('resize', () => {
		handleExpandCollapse(cardsEl, hasMore, hasLess, updateState);
	});
};

const findDraftAttachmentIdFromSysId = (sysId, attachmentList) =>
	attachmentList.find(attachment => attachment.sysId === sysId);

const handleAttachmentDelete = ({
	action: {
		payload: { sysId }
	},
	state: { attachments },
	dispatch
}) => {
	const attachmentToDelete = findDraftAttachmentIdFromSysId(sysId, attachments);
	if (attachmentToDelete) {
		dispatch(ATTACHMENT_CONSTANTS.FILE_DELETED, {
			sysId: attachmentToDelete.attachmentId
		});
	}
};

const deleteFile = createGraphQLEffect(
	ATTACHMENT_MUTATIONS.EMAIL_CLIENT_DELETE_ATTACHMENT,
	{
		variableList: ['sysId'],
		successActionType: ATTACHMENT_CONSTANTS.FILE_DELETE_SUCCEEDED,
		errorActionType: ATTACHMENT_CONSTANTS.FILE_DELETE_FAILED
	}
);

const handleAttachmentRename = ({
	action: {
		payload: { sys_id, file_name }
	},
	state: { attachments },
	dispatch,
	updateState
}) => {
	const attachmentToRename = findDraftAttachmentIdFromSysId(
		sys_id,
		attachments
	);
	const { name: originalName } = attachmentToRename;
	if (attachmentToRename && originalName !== file_name) {
		dispatch(ATTACHMENT_CONSTANTS.FILE_RENAMED, {
			sysId: attachmentToRename.attachmentId,
			name: file_name
		});
		updateState({
			attachments: attachments.map(item => {
				if (item.sysId === attachmentToRename.sysId) {
					item.name = file_name;
				}
				return item;
			}),
			attachmentEditing: {
				sysId: attachmentToRename.sysId,
				originalName: originalName,
				renameTo: file_name
			}
		});
	}
};

const renameFile = createGraphQLEffect(
	ATTACHMENT_MUTATIONS.EMAIL_CLIENT_RENAME_ATTACHMENT,
	{
		variableList: ['sysId', 'name'],
		successActionType: ATTACHMENT_CONSTANTS.FILE_RENAME_SUCCEEDED,
		errorActionType: ATTACHMENT_CONSTANTS.FILE_RENAME_FAILED
	}
);

const renameFileSuccess = ({
	action: { payload },
	state,
	dispatch,
	updateState
}) => {
	const errors = get(payload, 'errors', []);
	const {
		attachments,
		attachmentEditing: { originalName, renameTo, sysId }
	} = state;
	const names = [originalName, renameTo];
	if (errors.length) {
		updateState({
			attachmentEditing: null,
			attachments: attachments.map(item => {
				if (item.sysId === sysId) {
					item.name = originalName;
				}
				return item;
			})
		});
		showAlert('ATTACHMENT_RENAME_FAILED', dispatch, names);
	}
};

const renameFileFail = ({ dispatch, state, updateState }) => {
	const {
		attachmentEditing: { originalName, renameTo, sysId },
		attachments
	} = state;
	const names = [originalName, renameTo];
	updateState({
		attachmentEditing: null,
		attachments: attachments.map(item => {
			if (item.sysId === sysId) {
				item.name = originalName;
			}
			return item;
		})
	});
	showAlert('ATTACHMENT_RENAME_FAILED', dispatch, names);
};

const onDeleteFileSuccess = ({ action, state, updateState, dispatch }) => {
	const { payload } = action;
	if (payload.errors.length) {
		onDeleteFileFail({ action, dispatch });
	} else {
		const sysId = get(
			action,
			'payload.data.GlideEmail_Mutation.deleteAttachment'
		);
		const attachments = state.attachments.filter(
			file => file.attachmentId !== sysId
		);
		updateState({ attachments });
	}
};

const onDeleteFileFail = ({ action, dispatch }) => {
	const sysId = get(action, 'meta.options.variables.sysId');
	const formData = get(action, 'meta.request.data');
	const failedFile =
		formData && formData.getAll(ATTACHMENT_QUERY_CONSTANTS.FILE);
	const fileName = (failedFile && failedFile.name) || sysId;
	const nwError = get(action, 'payload.message', '');

	let content;
	if (nwError === 'Network request failed') {
		content = ErrorMap['ATTACHMENT_DELETE_NETWORK_ERROR'](fileName);
	} else {
		content = ErrorMap['ATTACHMENT_DELETE_FAILED'](fileName);
	}
	const defaultAlert = ErrorMap['DEFAULT_ERROR'];
	showAlert({ ...defaultAlert, ...content }, dispatch);
};

const getAttachmentsFromRecord = createGraphQLEffect(
	ATTACHMENTS_QUERY.RECORD_ATTACHMENT_QUERY,
	{
		variableList: ['table', 'sysId'],
		successActionType:
			ATTACHMENT_CONSTANTS.GET_ATTACHMENTS_FROM_RECORD_SUCCEEDED,
		errorActionType: ATTACHMENT_CONSTANTS.GET_ATTACHMENTS_FROM_RECORD_FAILED
	}
);

const onGetAttachmentsFromRecordSuccess = ({
	action: { payload },
	state,
	dispatch,
	updateState
}) => {
	const {config: {docViewerEnabled}} = state;
	const errors = get(payload, 'errors');
	const attachments = get(
		payload,
		'data.GlideAttachmentQuery_Query.attachments.records',
		[]
	);
	if (errors.length) {
		showAlert('ATTACHMENT_BROWSE_FAIL', dispatch);
	} else {
		dispatch(COMPOSITE_ACTIONS.LOAD_DOCUMENT_VIEWER, {
			docViewerEnabled
		});
		updateState({
			targetRecord: { ...state.targetRecord, attachments },
			openBrowseModal: true
		});
	}
};

const onGetAttachmentsFromRecordFail = ({ dispatch }) => {
	showAlert('ATTACHMENT_BROWSE_FAIL', dispatch);
};

const uploadFilesFromRecord = createGraphQLEffect(
	ATTACHMENT_MUTATIONS.UPLOAD_ATTACHMENT_FROM_TARGET_RECORD,
	{
		variableList: ['draftId', 'attachmentIds'],
		successActionType: ATTACHMENT_CONSTANTS.ATTACH_FILES_FROM_RECORD_SUCCESS,
		errorActionType: ATTACHMENT_CONSTANTS.ATTACH_FILES_FROM_RECORD_FAIL
	}
);
const getAttachments = items => {
	return items.map(item => {
		const { sysId, draftId } = item;
		return { ...item.attachment, draftId, attachmentId: sysId };
	});
};
const uploadFilesFromRecordSuccess = ({
	action: { payload },
	state,
	dispatch,
	updateState
}) => {
	const {config: {docViewerEnabled}} = state;
	let uploaded = get(
		payload,
		'data.GlideEmail_Mutation.addAttachmentsFromTargetRecord',
		[]
	);
	let { attachments } = state;

	if (payload.errors.length) {
		payload.errors.forEach(error => showAlert(error.message, dispatch));
	} else if (uploaded.length) {
		uploaded = getAttachments(uploaded);
		attachments = [...attachments, ...uploaded];
		updateState({ attachments });
		dispatch(COMPOSITE_ACTIONS.LOAD_DOCUMENT_VIEWER, {
			docViewerEnabled
		});
	} else {
		showAlert('ATTACHMENT_ADD_FROM_RECORD_FAIL', dispatch);
	}
};

const uploadFilesFromRecordFail = ({ dispatch }) => {
	showAlert('ATTACHMENT_ADD_FROM_RECORD_FAIL', dispatch);
};

const onBrowseFromRecordActionClick = ({
	action,
	state,
	dispatch,
	updateState
}) => {
	const actionId = get(action, 'payload.footerAction.id', '');
	updateState({ openBrowseModal: false });
	if (actionId === 'Add') {
		const { targetRecord, emailRecord, attachments } = state;

		const selectedIds = targetRecord.attachments
			.filter(item => item.selected)
			.map(item => item.sysId);
		const partitions = partition(selectedIds, id =>
			attachments.find(att => att.sysId === id)
		);
		const duplicates = partitions[0];
		const tobeUploaded = partitions[1];
		if (duplicates.length) {
			showAlert('ATTACHMENTS_ALREADY_EXISTS', dispatch);
		}
		if (tobeUploaded.length) {
			dispatch(ATTACHMENT_CONSTANTS.ATTACH_FILES_FROM_RECORD, {
				draftId: emailRecord.sysId,
				attachmentIds: tobeUploaded
			});
		}
	}
};

const handleAttachmentDownload = ({ action: { payload }, state }) => {
	const { sysId } = payload;
	const host = state.meta.host;
	const link = host.shadowRoot.querySelector('#download_attachment_link');
	if (sysId && link) {
		link.setAttribute('href', `/sys_attachment.do?sys_id=${sysId}`);
		link.click();
	}
};

const handleAttachmentModalCheckboxClicked = ({
	action: { payload },
	updateState,
	state: { targetRecord }
}) => {
	const { attachmentSelected } = payload;
	const { attachments } = targetRecord;
	const updatedAttachments = attachments.map(item => {
		if (item.sysId === attachmentSelected.sysId) {
			item.selected = payload.value;
		}
		return item;
	});
	updateState({
		targetRecord: {
			...targetRecord,
			attachments: updatedAttachments
		}
	});
};

const handleExtentionChange = ({ action: { payload }, dispatch }) => {
	const { notifications } = payload;
	const { message: content } = notifications.length ? notifications.pop() : '';
	const defaultAlert = ErrorMap['DEFAULT_ERROR'];
	if (content) {
		defaultAlert.content = content;
	}

	showAlert(defaultAlert, dispatch);
};

