import { RECIPIENT_CONSTANTS } from '../constants';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { EMAIL_QUERIES } from '../schemas/datasources';
import { debounce, reject } from 'lodash';
import { validateEmail } from './utils';
import { t } from 'sn-translate';

const recipientDataState = {};

const cancelSearch = opened => {
	if (!opened) {
		debounceFn.cancel();
	}
};

const setRecipientDataState = (emailAddress, dataState, displayName) => {
	if (emailAddress in recipientDataState) {
		//when same email address is used multiple time and in multiple field
		recipientDataState[emailAddress].dataStates.push(dataState);
	} else {
		recipientDataState[emailAddress] = {
			displayName: displayName || emailAddress,
			dataStates: [dataState]
		};
	}
};

const handleRecipentCollapse = ({
	action: {
		payload: { relatedTarget }
	},
	state: { recipients_cc, recipients_bcc },
	updateState
}) => {
	if (
		(!relatedTarget ||
			(relatedTarget.label !== t('Cc') && relatedTarget.label !== t('Bcc'))) &&
		(!recipients_cc.length && !recipients_bcc.length)
	) {
		updateState({ showCcBcc: false });
	}
};

const searchResults = (dispatch, sysId, qs, updateState) => {
	if (qs.length >= RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_SEARCH_MIN_CHAR) {
		dispatch(RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_SEARCHED, {
			chars: qs,
			draftId: sysId
		});
	} else {
		updateState({
			recipientSearchResults: [],
			opened: false
		});
	}
};

const debounceFn = debounce(
	searchResults,
	RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_SEARCH_DELAY
);

const handleRecipientSearch = ({
	action: {
		payload: { qs }
	},
	state: {
		emailRecord: { sysId }
	},
	updateState,
	dispatch
}) => {
	debounceFn(dispatch, sysId, qs, updateState);
};

const fetchRecipients = createGraphQLEffect(
	EMAIL_QUERIES.EMAIL_CLIENT_RECIPIENT_QUERY,
	{
		variableList: ['chars', 'draftId'],
		successActionType: RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_FETCH_SUCCESS,
		errorActionType: RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_FETCH_ERROR
	}
);

const handleRecipientFetchSuccess = ({ action: { payload }, updateState }) => {
	const result = payload.data.GlideEmail_Query.searchRecipients;
	const opened = result.length ? true : false;
	updateState({
		recipientSearchResults: result,
		opened: opened
	});
};

const handleRecipientFetchError = ({ action: { payload } }) => {
	console.error(
		'ERROR',
		payload.error
	); /* eslint no-console: ["error", { allow: ["warn", "error"] }] */
};

const checkEmailAddressValidityStart = ({
	action: {
		payload: {
			emailAddress,
			dataState,
			displayName,
			recipientSource,
			recipientSourceTable
		}
	},
	dispatch,
	updateState,
	state: { config, showCcBcc, opened }
}) => {
	if (
		(dataState === 'recipients_cc' || dataState === 'recipients_bcc') &&
		!showCcBcc
	) {
		updateState({ showCcBcc: true });
	}
	if (!validateEmail(emailAddress)) {
		cancelSearch(opened);
		updateState({
			recipientSearchResults: [],
			opened: false
		});

		if (typeof emailAddress === 'undefined' || emailAddress === '') {
			if (recipientSource && recipientSourceTable) {
				updateState({
					path: `${dataState}`,
					value: {
						id: displayName,
						emailAddress: '',
						isValid: true,
						displayName: displayName,
						invalidReason: '',
						recipientSource: recipientSource,
						recipientSourceTable: recipientSourceTable
					},
					operation: 'push'
				});
			}
		} else {
			updateState({
				path: `${dataState}`,
				value: {
					id: emailAddress,
					emailAddress: emailAddress,
					isValid: false,
					displayName: emailAddress,
					invalidReason: RECIPIENT_CONSTANTS.RECIPIENT_INVALID_REASONS.get(
						'Invalid email address'
					)
				},
				operation: 'push'
			});
		}
	} else {
		setRecipientDataState(emailAddress, dataState, displayName);
		dispatch(RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_VALIDATION_CHECK, {
			emailAddress: emailAddress,
			emailAddressFilterIds: config.emailAddressFilters
		});
	}
};

const validateEmailAddress = createGraphQLEffect(
	EMAIL_QUERIES.EMAIL_CLIENT_ADDRESS_VALIDATION,
	{
		variableList: ['emailAddress', 'emailAddressFilterIds'],
		successActionType:
			RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_VALIDATION_CHECK_SUCCES,
		errorActionType: RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_VALIDATION_CHECK_ERROR
	}
);

const checkEmailAddressValiditySuccess = ({
	action: { payload },
	state,
	updateState
}) => {
	const {
		emailAddress,
		isValid,
		invalidReason
	} = payload.data.GlideEmail_Query.validateAddress;
	const emailId = emailAddress || state.searchString;
	const { displayName, dataStates } = recipientDataState[emailId] || {};
	const dataState = dataStates ? dataStates.pop() : '';
	updateState({
		path: `${dataState}`,
		value: {
			id: emailId,
			emailAddress: emailId,
			isValid: isValid,
			displayName: displayName,
			invalidReason: RECIPIENT_CONSTANTS.RECIPIENT_INVALID_REASONS.get(
				invalidReason
			)
		},
		operation: 'push'
	});
	updateState({
		recipientSearchResults: [],
		opened: false
	});
};

const checkEmailAddressValidityError = ({ action: { payload } }) => {
	console.error(
		'ERROR',
		payload.error
	); /* eslint no-console: ["error", { allow: ["warn", "error"] }] */
};

const handleRecipientUpdateState = ({ action: { payload }, updateState }) => {
	updateState({
		...payload
	});
};

const removeEmailRecipient = ({
	action: {
		payload: { itemIndex, dataState }
	},
	state,
	updateState
}) => {
	const newRecipientList = reject(
		state[dataState],
		(value, index) => index === itemIndex
	);
	updateState({
		path: `${dataState}`,
		value: newRecipientList,
		operation: 'set'
	});
};

export default {
	[RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_FETCH_STARTED]: {
		effect: handleRecipientSearch,
		stopPropagation: true
	},
	[RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_SEARCHED]: {
		...fetchRecipients,
		stopPropagation: true
	},
	[RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_FETCH_SUCCESS]: {
		effect: handleRecipientFetchSuccess,
		stopPropagation: true
	},
	[RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_FETCH_ERROR]: {
		effect: handleRecipientFetchError,
		stopPropagation: true
	},
	[RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_VALIDATION_STARTED]: {
		effect: checkEmailAddressValidityStart,
		stopPropagation: true
	},
	[RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_VALIDATION_CHECK]: {
		...validateEmailAddress,
		stopPropagation: true
	},
	[RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_VALIDATION_CHECK_SUCCES]: {
		effect: checkEmailAddressValiditySuccess,
		stopPropagation: true
	},
	[RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_VALIDATION_CHECK_ERROR]: {
		effect: checkEmailAddressValidityError,
		stopPropagation: true
	},
	[RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_UPDATE_STATE]: {
		effect: handleRecipientUpdateState,
		stopPropagation: true
	},
	[RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_REMOVED]: {
		effect: removeEmailRecipient,
		stopPropagation: true
	},
	[RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_CC_BCC_COLLAPSE]: {
		effect: handleRecipentCollapse,
		stopPropagation: true
	}
};
