import { NOW_ACTIONS } from '../../constants/nowConstants';
import { HEADER_BUTTON_ACTIONS } from './constants';

const handleHeaderButtonClicked = ({ action: { payload }, dispatch }) =>
	dispatch(HEADER_BUTTON_ACTIONS.HEADER_ACTION_BUTTON_CLICKED, {
		actionType: payload.headerType
	});

export default {
	[NOW_ACTIONS.NOW_BUTTON_CLICKED]: {
		effect: handleHeaderButtonClicked,
		stopPropagation: true
	}
};
