const GLIDE_RECORD_QUERY = 'GlideRecord_Query';
const GLIDE_EMAIL_QUERY = 'GlideEmail_Query';

const RESULTS = '_results';
const REFERENCE = '_reference';
const GLIDE_EMAIL_DOC_VIEWER_ENABLED_CHECK = 'getEmailTargetTableDocViewerFlag';

const DOC_VIEWER_FLAG_QUERY = `
query($table: String!) {
	${GLIDE_EMAIL_QUERY} {
		${GLIDE_EMAIL_DOC_VIEWER_ENABLED_CHECK}(table: $table) {
			isEnabled
		}
	}
}
`;

const EMAIL_CLIENT_RETRIEVE_SENT_EMAIL = `
query($sentEmailId: String!, $queryCondition: String!) {
	${GLIDE_RECORD_QUERY} {
		sys_email (sys_id: $sentEmailId) {
			${RESULTS} {
				body {
					value
				}
				body_text {
					value
				}
				content_type {
					value
				}
				sys_created_on {
					displayValue
				}
				sys_id {
					value
				}
				importance {
					value
				}
				subject {
					value
				}
				target_table {
					value
				}
				instance {
					value
				}
				type {
					value
				}
				user_id {
					${REFERENCE} {
						name {
							value
						}
						avatar {
							value
						}
					}
				}
				direct {
					value
				}
				copied {
					value
				}
				blind_copied {
					value
				}
			}
		}
		sys_email_attachment(queryConditions: $queryCondition, pagination: {limit: 30, offset: 0}) {
			${RESULTS} {
				file_name {
					value
				}
				content_disposition {
					value
				}
				attachment {
					${REFERENCE} {
						file_name {
							value
						}
						state {
							value
						}
						sys_id {
							value
						}
						size_bytes {
							value
						}
						content_type {
							value
						}
					}
				}
			}
		}
	}
}
`;

const RETRIEVE_RECIPIENT_INFORMATION = `
query($queryCondition: String!) {
	${GLIDE_RECORD_QUERY} {
		sys_user(queryConditions: $queryCondition) {
			_results {
				name {
					value
				}
				email {
					value
				}
			}
		}
	}
}`;

export const EMAIL_VIEWER_QUERIES = Object.freeze({
	EMAIL_CLIENT_RETRIEVE_SENT_EMAIL,
	RETRIEVE_RECIPIENT_INFORMATION,
	DOC_VIEWER_FLAG_QUERY
});
