import { EMAIL_COMPOSER } from './constants';
import {
	COMPOSITE_ACTIONS,
	EMAIL_DRAFT_TYPES
} from '../../constants/emailConstants';
import compositeActionHandlers from '../../commons/utils/compositeActionHandlers';
import { QUICK_MESSAGE_INSERT } from '../now-email-client-quick-messages/constants';
import { t } from 'sn-translate';
import previewActionHandlers from '../../commons/EmailAttachments/AttachmentPreview/actionHandlers';
import { getEmailClientSidebarActions } from './utils';
import { loadDynamicSidebarComponents } from './sidebarDependencymanager';
import { dispatchClassicAndUibActions } from '../../commons/workspaceActionMap';

const TAB_DISPLAY_VALUE_MAP = new Map([
	[EMAIL_DRAFT_TYPES.NEW_DRAFT, t('New Email Draft')],
	[EMAIL_DRAFT_TYPES.NEW_RESPONSE_DRAFT, t('New Response Draft')]
]);

const handleEmailDraftInitialised = ({ 
	dispatch, 
	action: { payload },
	state,
	updateState
}) => {
	const { table, sysId, subject, type } = payload;
	const { properties: { dirtyFields }, meta: { host } } = state;
	loadDynamicSidebarComponents(dispatch)
	const sideBarActions = getEmailClientSidebarActions(
		dirtyFields,
		state
	);
	updateState({sideBarActions});
	dispatch(EMAIL_COMPOSER.EMAIL_DRAFT_INITIALISED, {
		table: table,
		sysId: sysId,
		subject: subject
	});
	if (type !== EMAIL_DRAFT_TYPES.EXISTING_DRAFT) {
		dispatchClassicAndUibActions(host, 'RECORD_STATUS_CHANGED', dispatch, {
			table: table,
			sys_id: sysId,
			status: 'inserted'
		});
		dispatchClassicAndUibActions(host, 'FORM_UPDATE_HEADER', dispatch, {
			table: table,
			sysId: sysId,
			recordDisplayValue: TAB_DISPLAY_VALUE_MAP.get(type)
		});
	}
};

const handleEmailDraftModified = ({ dispatch, action: { payload }, state }) => {
	const { sysId, table } = payload;
	const { meta: { host } } = state;
	dispatch(EMAIL_COMPOSER.EMAIL_DRAFT_MODIFIED, {
		table: table,
		sysId: sysId
	});
	dispatchClassicAndUibActions(host, 'RECORD_STATUS_CHANGED', dispatch, {
		table: table,
		sys_id: sysId,
		status: 'modified'
	});
};

const handleEmailDraftSaved = ({ dispatch, action: { payload }, state }) => {
	const { sysId, table, recordUpdate } = payload;
	const { meta: { host }, subject } = state;
	dispatch(EMAIL_COMPOSER.EMAIL_DRAFT_SAVE_SUCCESS, {
		table: table,
		sysId: sysId
	});
	dispatchClassicAndUibActions(host, 'FORM_UPDATE_HEADER', dispatch, {
		table: table,
		sysId: sysId,
		recordDisplayValue: subject
	});
	if (recordUpdate) {
		dispatchClassicAndUibActions(host, 'RECORD_STATUS_CHANGED', dispatch, {
			table: table,
			sys_id: sysId,
			status: 'updated'
		});
	}
};

const handleEmailDraftSend = ({
	dispatch,
	action: { payload },
	state
}) => {
	const { sysId, table } = payload;
	const { properties: { tabId }, meta: { host } } = state;
	dispatch(EMAIL_COMPOSER.EMAIL_DRAFT_SEND_SUCCESS, {
		table: table,
		sysId: sysId
	});
	dispatchClassicAndUibActions(host, 'UPDATE_ONE_TAB', dispatch, {
		type: 'UPDATE_ONE_TAB_CLOSE',
		tab_id: tabId
	});
};

const handleSidebarOpened = ({ updateState, state }) => {
	updateState({ sidebarExpanded: !state.sidebarExpanded });
};

const handleSidebarUpdate = ({ updateState, state }) => {
	const { properties: { dirtyFields } } = state;
	const sideBarActions = getEmailClientSidebarActions(
		dirtyFields,
		state
	);
	updateState({sideBarActions});
}

const handleQuickMessageInsert = ({ action: { payload }, updateState }) => {
	const { message, isInsert } = payload;
	updateState({ quickMessages: { message, isInsert } });
};

export default {
	...compositeActionHandlers,
	...previewActionHandlers,
	[COMPOSITE_ACTIONS.EMAIL_DRAFT_INITIALISED]: {
		effect: handleEmailDraftInitialised,
		stopPropagation: true
	},
	[COMPOSITE_ACTIONS.EMAIL_DRAFT_MODIFIED]: {
		effect: handleEmailDraftModified,
		stopPropagation: true
	},
	[COMPOSITE_ACTIONS.EMAIL_SUCCESSFULLY_SAVED]: {
		effect: handleEmailDraftSaved,
		stopPropagation: true
	},
	[COMPOSITE_ACTIONS.EMAIL_SUCCESSFULLY_SEND]: {
		effect: handleEmailDraftSend,
		stopPropagation: true
	},
	[EMAIL_COMPOSER.SIDEBAR_OPENED]: {
		effect: handleSidebarOpened,
		stopPropagation: true
	},
	[QUICK_MESSAGE_INSERT]: {
		effect: handleQuickMessageInsert,
		stopPropagation: true
	},
	[EMAIL_COMPOSER.SIDEBAR_COMPONENTS_UPDATED]: {
		effect: handleSidebarUpdate,
		stopPropagation: true
	}
};
