import { groupBy } from 'lodash';
export const onDrop = (e, ed) => {
	const host = ed.targetElm.getRootNode().host;
	const files = Array.from(e.dataTransfer.files);
	const fileGroups = groupBy(files, function(file) {
		if (/image\/(gif|jpe?g|png|bmp|tiff)/gi.test(file.type)) {
			return 'images';
		} else {
			return 'others';
		}
	});

	const imgFiles = fileGroups.images;
	const nonImgFiles = fileGroups.others;
	// add inline image tag for tiff explicitly
	if (imgFiles) {
		let imgNodes = [];
		imgFiles.forEach(file => {
			let imgNode = document.createElement('img');
			imgNode.dataset.fileName = file.name;
			ed.selection.setNode(imgNode);
			imgNode = ed.selection
				.getNode()
				.querySelector(`[data-file-name="${file.name}"]:not([data-sys-id])`);
			imgNodes.push(imgNode);
		});
		host.dispatchEvent(
			new CustomEvent('image-drop', {
				detail: { imgs: imgNodes, files: imgFiles }
			})
		);
	}
	if (nonImgFiles) {
		host.dispatchEvent(
			new CustomEvent('file-drop', { detail: { files: nonImgFiles } })
		);
	}
	return ed.dom.events.cancel(e);
};
