import { t } from 'sn-translate';
import { collapseCards, expandCards } from '../utils';
import {
	ATTACHMENT_POSITION,
	ATTACHMENT_CONSTANTS,
} from '../constants';
import '@servicenow/now-checkbox';
import '@servicenow/now-record-common';

const getAttachmentItem = (attachment, currentUser, canEdit) => {
	return {
		canDelete: canEdit,
		canEditName: canEdit,
		name: attachment.name,
		size: attachment.size,
		state: !attachment.uploading ? attachment.state : "uploading",
		sysId: attachment.sysId,
		type: attachment.type,
		userId: currentUser ? currentUser.sys_id : '',
		userName: currentUser ? currentUser.userName : ''
	};
};

export const getAttachmentCard = (
	attachment,
	{ currentUser },
	{ canEdit },
	position
) => {
	if (position === ATTACHMENT_POSITION.MODAL) {
		return (
			<div className="ec-attachment-modal">
				<div className="ec-attachment-modal-checkbox">
					<now-checkbox
						checked={attachment.selected}
						id={`attachment-checkbox-${attachment.sysId}`}
						append-to-payload={{
							dispatchedEvent:
								ATTACHMENT_CONSTANTS.ATTACHMENT_MODAL_CARD_CHECKED,
							attachmentSelected: attachment
						}}
						manage-checked
						config-aria={{
							'aria-label': attachment.selected
								? t('Click to deselect {0}', attachment.name)
								: t('Click to select {0}', attachment.name)
						}}
					/>
				</div>
				<div className="ec-attachment-modal-card">
					<now-record-common-attachment-card
						id={`attachment-card-${attachment.sysId}`}
						can-create={canEdit}
						className="ec-attachment-cards-modal"
						item={getAttachmentItem(attachment, currentUser, canEdit)}
						max-file-name-length={100}
						mode="compact"
					/>
				</div>
			</div>
		);
	}
	return (
		<div className="ec-attachment-card">
			<now-record-common-attachment-card
				can-create={canEdit}
				item={getAttachmentItem(attachment, currentUser, canEdit)}
				max-file-name-length={100}
				mode="compact"
				className="ec-attachment-cards-body"
				display-upload-progress-bar={attachment.uploading}
			/>
		</div>
	);
};

export const createAttachmentCards = (
	attachments,
	config,
	state,
	updateState,
	position = ATTACHMENT_POSITION.LAYOUT
) => {
	let { hasMore, hasLess, cardsStatus } = state;

	if (config.scroll) {
		hasMore = hasLess = false;
		cardsStatus = 'normal';
	}

	return (
		<div>
			<div className={`ec-attachments-cards ${cardsStatus} ${position}`}>
				{attachments.map(attachment => getAttachmentCard(attachment, state, config, position))}
			</div>
			{hasMore || hasLess ? (
				<now-button-bare
					id="email-attachments-show-more-less"
					className="show-more"
					label={hasMore ? t('Show More') : t('Show Less')}
					size="sm"
					tooltip-content={hasMore ? t('Show More') : t('Show Less')}
					config-aria={{
						'aria-label': hasMore ? t('Show More') : t('Show Less')
					}}
					on-click={() => {
						if (hasMore) {
							expandCards(updateState);
						} else if (hasLess) {
							collapseCards(updateState);
						}
					}}
				/>
			) : (
				''
			)}
			<a
				id="download_attachment_link"
				className="ec-hidden"
				href="/sys_attachment.do"
				target="_blank"
				download>
				Download
			</a>
		</div>
	);
};
