import { RECIPIENT_CONSTANTS } from '../../constants';
import { COMPOSITE_ACTIONS } from '../../../../constants/emailConstants';
import { actionTypes } from '@servicenow/ui-core';
const { COMPONENT_DOM_READY, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const handleDropdownItemActive = ({
	action: {
		payload: { activeItem }
	},
	updateState
}) => {
	updateState({
		activeItem: activeItem
	});
};

const handleDropdownItemClick = ({
	host,
	action: {
		payload: { dataState, item }
	},
	dispatch
}) => {
	dispatch(COMPOSITE_ACTIONS.EMAIL_DRAFT_FIELD_MODIFIED);
	dispatch(RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_VALIDATION_STARTED, {
		...item,
		dataState
	});
	const input = host.shadowRoot.querySelector('input');
	if (input) {
		input.value = '';
		input.focus();
	}
};

const handleDomReady = ({ host, properties }) => {
	if (properties.autofocus) {
		setTimeout(() => {
			const input = host.shadowRoot.querySelector('input');
			if (input) {
				input.focus();
			}
		}, 100);
	}
};

const handlePropertyChange = ({ properties, state, updateState }) => {
	const { opened } = properties;
	const { activeItem } = state;
	if (opened && activeItem) {
		//reset activeItem
		updateState({
			activeItem: null
		});
	}
};

export default {
	[RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_DROPDOWN_ITEM_SET_ACTIVE]: {
		effect: handleDropdownItemActive,
		stopPropagation: true
	},
	[RECIPIENT_CONSTANTS.EMAIL_RECIPIENT_DROPDOWN_ITEM_CLICKED]: {
		effect: handleDropdownItemClick,
		stopPropagation: true
	},
	[COMPONENT_DOM_READY]: {
		effect: handleDomReady,
		stopPropagation: true
	},
	[COMPONENT_PROPERTY_CHANGED]: {
		effect: handlePropertyChange,
		stopPropagation: true
	},
	['NOW_POPOVER#OPENED_SET']: {
		stopPropagation: true
	}
};
