/* eslint-disable no-console */
import {
	COMPOSITE_ACTIONS,
	EMAIL_TABLES
} from '../../constants/emailConstants';
import { NOW_ACTIONS, ALERT_CONSTANTS } from '../../constants/nowConstants';
import { HEADER_BUTTON_ACTIONS } from '../../components/sn-email-client-internal-header/constants';
import { EMAIL_MINI_COMPOSER_ACTIONS } from '../../components/now-email-client-mini-composer-connected/constants';
import {t} from 'sn-translate';
import { actionTypes } from '@servicenow/ui-core';
import { dispatchClassicAndUibActions } from '../workspaceActionMap';

const handleEmailRecordInitialised = ({
	action: { payload },
	updateState,
	dispatch
}) => {
	const { subject, recordId, targetTable, targetRecord, draftType } = payload;
	updateState({
		subject,
		recordId,
		targetRecord,
		targetTable
	});
	dispatch(COMPOSITE_ACTIONS.EMAIL_DRAFT_INITIALISED, {
		table: EMAIL_TABLES.DRAFT_EMAIL,
		sysId: recordId,
		subject: subject,
		type: draftType
	});
};

const handleEmailFieldModified = ({
	state,
	dispatch,
	updateState,
	action: { payload }
}) => {
	if (payload) {
		const { subject } = payload;
		updateState({ subject: subject });
	}
	const { recordModified, recordId } = state;
	if (!recordModified) {
		dispatch(COMPOSITE_ACTIONS.EMAIL_DRAFT_MODIFIED, {
			table: EMAIL_TABLES.DRAFT_EMAIL,
			sysId: recordId
		});
		updateState({ recordModified: true });
	}
};

const handleHeaderButtonClicked = ({ action: { payload }, updateState }) => {
	const { actionType } = payload;
	if (actionType === 'save') {
		updateState({ initiateSave: true });
	} else if (actionType === 'send') {
		updateState({ initiateSend: true });
	}
};

const handleSendSuccess = ({ updateState, dispatch, state }) => {
	const { recordId } = state;
	updateState({ initiateSend: false });
	dispatch(COMPOSITE_ACTIONS.EMAIL_SUCCESSFULLY_SEND, {
		table: EMAIL_TABLES.DRAFT_EMAIL,
		sysId: recordId
	});
};

const handleSendFail = ({ updateState }) => {
	updateState({ initiateSend: false });
};

const handleSendCancel = ({ updateState }) =>
	updateState({ initiateSend: false });

const handleSaveSuccess = ({ updateState, dispatch, state }) => {
	const { recordId, 
			recordModified, 
			popOutButtonClicked, 
			meta: { host }
		} = state;
	updateState({ initiateSave: false, recordModified: false });
	if (popOutButtonClicked) {
		//For mini email composer if pop out button is clicked.
		updateState({ popOutButtonClicked: false });
		dispatch(EMAIL_MINI_COMPOSER_ACTIONS.EMAIL_CLIENT_POP_OUT_SUCCEEDED);
		dispatchClassicAndUibActions(host, 'PREVIEW_RECORD', dispatch, {
			table: EMAIL_TABLES.DRAFT_EMAIL,
			sys_id: recordId
		});
	} else {
		dispatch(NOW_ACTIONS.ADD_TO_ALERT_LIST, {
			alerts: [
				{
					id: `now_email_alert_${Date.now()}`,
					status: ALERT_CONSTANTS.STATUS.POSITIVE,
					icon: ALERT_CONSTANTS.ICONS.POSITIVE,
					content: t('Email draft saved.'),
					action: { type: ALERT_CONSTANTS.ACTION.TYPES.DISMISS }
				}
			]
		});
		dispatch(COMPOSITE_ACTIONS.EMAIL_SUCCESSFULLY_SAVED, {
			table: EMAIL_TABLES.DRAFT_EMAIL,
			sysId: recordId,
			recordUpdate: recordModified
		});
	}
};

const handleSaveFail = ({ updateState, state }) => {
	const { popOutButtonClicked } = state;
	updateState({ initiateSave: false });
	if (popOutButtonClicked) {
		//For mini email composer if pop out button is clicked.
		updateState({ popOutButtonClicked: false });
	}
};

const handleSaveCancel = ({ updateState, state }) => {
	const { popOutButtonClicked } = state;
	updateState({ initiateSave: false });
	if (popOutButtonClicked) {
		//For mini email composer if pop out button is clicked.
		updateState({ popOutButtonClicked: false });
	}
};

const addToAlertList = ({ action: { payload }, updateState }) => 
	updateState({
		path: 'alertList',
		value: payload.alerts,
		operation: 'concat'
	});

const dismissAlertEffect = ({ action, updateState }) =>
	updateState({
		alertList: action.payload.value || []
	});

const handleShowDetailsButtonClicked = ({ updateProperties, state }) => {
	const {
		properties: { showEmailDetails }
	} = state;
	updateProperties({ showEmailDetails: !showEmailDetails }); //I hate doing this
};

const removeExistingDraft = ({ updateState }) => {
	updateState({ 
		alertList: [],
		subject: null,
		recordModified: false,
		recordId: '',
		targetRecord: '',
		targetTable: ''
	});
}

const handleComponentBootstrap = ({ action: { payload }, updateState }) => {
	updateState({
		path: 'meta',
		value: {
			root: payload.host.shadowRoot,
			host: payload.host
		},
		operation: 'set'
	});
}

export default {
	[COMPOSITE_ACTIONS.EMAIL_RECORD_INITIALISED]: {
		effect: handleEmailRecordInitialised,
		stopPropagation: true
	},
	[COMPOSITE_ACTIONS.EMAIL_DRAFT_FIELD_MODIFIED]: {
		effect: handleEmailFieldModified,
		stopPropagation: true
	},
	[HEADER_BUTTON_ACTIONS.HEADER_ACTION_BUTTON_CLICKED]: {
		effect: handleHeaderButtonClicked,
		stopPropagation: true
	},
	[COMPOSITE_ACTIONS.EMAIL_DRAFT_RECORD_SEND_SUCESS]: {
		effect: handleSendSuccess,
		stopPropagation: true
	},
	[COMPOSITE_ACTIONS.EMAIL_DRAFT_RECORD_SEND_FAILED]: {
		effect: handleSendFail,
		stopPropagation: true
	},
	[COMPOSITE_ACTIONS.EMAIL_DRAFT_RECORD_SEND_CANCELED]: {
		effect: handleSendCancel,
		stopPropagation: true
	},
	[COMPOSITE_ACTIONS.EMAIL_DRAFT_RECORD_SAVE_SUCESS]: {
		effect: handleSaveSuccess,
		stopPropagation: true
	},
	[COMPOSITE_ACTIONS.EMAIL_DRAFT_RECORD_SAVE_FAILED]: {
		effect: handleSaveFail,
		stopPropagation: true
	},
	[COMPOSITE_ACTIONS.EMAIL_DRAFT_RECORD_SAVE_CANCELED]: {
		effect: handleSaveCancel,
		stopPropagation: true
	},
	[COMPOSITE_ACTIONS.EMAIL_SHOW_HIDE_BUTTON_CLICKED]: {
		effect: handleShowDetailsButtonClicked,
		stopPropagation: true
	},
	[NOW_ACTIONS.ADD_TO_ALERT_LIST]: {
		effect: addToAlertList,
		stopPropagation: true
	},
	[NOW_ACTIONS.NOW_ALERT_LIST_UPDATED]: {
		effect: dismissAlertEffect,
		stopPropagation: true
	},
	[COMPOSITE_ACTIONS.EMAIL_DRAFT_REINITIALISE_TRIGGERED]: {
		effect: removeExistingDraft,
		stopPropagation: true
	},
	[actionTypes.COMPONENT_BOOTSTRAPPED]: {
		effect: handleComponentBootstrap,
		stopPropagation: true
	}
};
