import { FROM_CONSTANTS } from '../constants';
import { NOW_ACTIONS } from '../../../constants/nowConstants';
import { COMPOSITE_ACTIONS } from '../../../constants/emailConstants';

const handleFromDropdownAction = ({
	action: { payload },
	updateState,
	dispatch
}) => {
	if (
		payload.nowAction.type ===
		NOW_ACTIONS.NOW_DROPDOWN_CUSTOM_TARGET_ITEM_CLICKED
	) {
		dispatch(COMPOSITE_ACTIONS.EMAIL_DRAFT_FIELD_MODIFIED);
		updateState({ fromValue: payload.item.label });
	}
};

export default {
	[FROM_CONSTANTS.EMAIL_FROM_DROPDOWN_ACTION]: {
		effect: handleFromDropdownAction,
		stopPropagation: true
	}
};
